import { Component, OnInit, ViewChild, TemplateRef, Input, Output, EventEmitter } from '@angular/core';
import { BsModalService } from 'ngx-bootstrap/modal';
import { BsModalRef } from 'ngx-bootstrap/modal/bs-modal-ref.service';
import * as $ from 'jquery';
// import { Ng2CropperService } from '../../../../../shared/components/ng2-cropper/ng2-cropper.service';
// import { SharedServices } from '../../../../../shared/services/services.module';
import { Ng2CropperService } from '../ng2-cropper/ng2-cropper.service';
import { SharedServices } from '../../services/services.module';

@Component({
  selector: 'app-image-upload',
  templateUrl: './image.upload.component.html',
  styleUrls: ['./image.upload.component.css']
})
export class ImageUploadComponent implements OnInit {
  @Input() imgUploadInp: any;  //<obj> = {cropping: true/false}
  @Output() onDoneImageSelect = new EventEmitter<any>();
  imageSrc: any = '';
  imageSrcToSend: any = '';
  dataObj: any = {};
  croppedImage: any = {
    x: '',
    y: '',
    width: '',
    height: ''
  }
  imageObj: any = {
    from: '', //['computer','library']
    fileDetailsFromComp: {
      name: '',
      desc: '',
      size: '',
      type: ''
    }
  }

  private file;
  private data;
  options = {
    maximize: true,
    aspectRatio: NaN,
    viewMode: 2,
    crop: (dataNew) => {
      this.data = dataNew || this.data;
      this.setCroppedDeatils(this.data);
    }
  };

  @ViewChild('imageViewModal') imageViewModal;
  @ViewChild('libraryModal') libraryModal;
  modalRef: BsModalRef;

  openModal(template: TemplateRef<any>, size: string) {
    this.modalRef = this.modalService.show(template, { class: size });
  }

  hideModal(): void {
    this.modalRef.hide();
    this.onDoneImageSelect.emit();
  }

  constructor(
    private modalService: BsModalService,
    private cropper: Ng2CropperService,
    private services: SharedServices
    ) {
  }

  ngOnInit() {
  }

  fileChangeListener(event){
    this.imageObj.from = 'computer';
    let file = event.target.files[0];
    let fileSize = file.size;
    fileSize = fileSize / (1024 * 1024);
    if (fileSize > 50) {
        this.services.toastr.error("File Size cannot be greater than 20MB");
        return false;
    }
    this.imageObj.fileDetailsFromComp.desc = file.name.split('.')[0];
    this.imageObj.fileDetailsFromComp.name = file.name;
    this.imageObj.fileDetailsFromComp.size = (Math.round(fileSize * 100) / 100) + 'MB';
    this.imageObj.fileDetailsFromComp.type = file.type;
    this.cropper.encode((this.file = event.target.files[0])).then((dataUrl) => {
     this.imageSrc = dataUrl;
   });
   if(!this.imgUploadInp.cropping){
     this.saveImageDetails();
   }
  }

  selFromLibrary(){
    this.imageObj.from = 'library';
    this.openModal(this.imageViewModal, 'modal-lg');
  }

  setCroppedDeatils(data){
    this.croppedImage.x = data.x;
    this.croppedImage.y = data.y;
    this.croppedImage.width = parseInt(data.width);
    this.croppedImage.height = parseInt(data.height);
  }

  setSize(type: string){
    let data: any;
    if(type == "height"){
      data = {'height': parseInt(this.croppedImage.height)}
    }else{
      data = {'width': parseInt(this.croppedImage.width)}
    }
    $('#imageToBeCropped').cropper("setData", data);
    let details = $('#imageToBeCropped').cropper("getData");
    this.setCroppedDeatils(details);
  }

  zoomOut(){
    $('#imageToBeCropped').cropper("zoom", -0.1);
  }

  zoomIn(){
    $('#imageToBeCropped').cropper("zoom", 0.1);
  }

  keyUpOnImageUrl(e: any, str: string){
    if(e.which == 13){
      this.uploadImageFromUrl(str);
    }
  }

  uploadImageFromUrl(str: string){
    this.imageSrc = str;
    this.imageSrcToSend = this.imageSrc;
  }

  changeImage(){
    this.imageSrc = '';
  }

  delImage(){
    this.imageSrc = '';
  }

  onFileSelect($event: any){
    this.modalRef.hide();
    console.log('image-upload', $event);
    this.dataObj = $event;
    this.imageSrc = this.dataObj.allData[this.dataObj.fileId].actualUrl;
    this.imageSrcToSend = this.imageSrc;
    if(!this.imgUploadInp.cropping){
      this.saveImageDetails();
    }
  }

  onFolderSelect(e: any){
    this.modalRef.hide();
    console.log('folder-select', e);
    let fileData: any = {};
    fileData.libId = e.grpId;
    fileData.libType = e.grpType;
    fileData.folderId = e.selFolderId;
    fileData.fileDesc = this.imageObj.fileDetailsFromComp.desc;
    let toSendPutFormdata = new FormData();
    toSendPutFormdata.append('data', JSON.stringify(fileData));
    toSendPutFormdata.append('file', this.file);
    this.services.resource.saveImgFileInLibrary(toSendPutFormdata).subscribe((data) => {
      console.log('upload file success');
      this.imageSrcToSend = data.data.actualUrl;
      this.onDoneImageSelect.emit({url: this.imageSrcToSend, size: this.croppedImage});
    })
  }

  saveImageDetails(){
    if(this.imageObj.from == 'computer'){
      this.openModal(this.libraryModal, 'modal-lg');
    }else{
      this.onDoneImageSelect.emit({url: this.imageSrcToSend, size: this.croppedImage});
    }
  }

}



