import { Injectable } from '@angular/core';

@Injectable()
export class DropdownService {

  constructor() { }

  getStyleTag(qId: string, color: string, selColor: string, selBgColor: string){
    let style: string = `
      <style id="s-${qId}">
        #q-${qId} .select2-container--default .select2-selection--single{
          border-bottom: 1px solid ${color}!important
        }
        .s-${qId}.select2-container--default .select2-results__option[aria-selected=true]{
          background-color: ${selBgColor}!important;
          color: ${selColor}!important;
        }
        .s-${qId} .select2-results__option{
          border: 1px solid ${color}!important;
          color: ${color}!important;
        }
      </style>
    `;
    return style;
  }


//styling for background of dropdown
  // .s-${qId} .select2-dropdown{
  //   background-color: ${bgColor}!important;
  // }
  // .s-${qId} .select2-search--dropdown .select2-search__field{
  //   background-color: ${bgColor}!important;
  // }

}



