import { Component, OnInit, Input, Output, OnChanges, SimpleChanges, EventEmitter } from '@angular/core';

import { QuestionTypesService } from '../../question-types.service';
import { SharedServices } from '../../../services/services.module';
import { RankingService } from '../ranking.service';
import { SurveyValidationService } from '../../../services/validation.module';

@Component({
  selector: 'app-ranking-item',
  templateUrl: './ranking-item.component.html',
  styleUrls: ['../../question-templates.scss', './ranking-item.component.scss']
})
export class RankingItemComponent implements OnInit, OnChanges {
  @Input() completeQuestion: any;
  @Input() data: any;
  @Input() changesApplied: boolean;
  @Input() item: any;
  @Input() langDirection: string;
  @Input() optTheme: any;
  @Output() onChange: EventEmitter<any> = new EventEmitter<any>();
  opt: any;
  columns: any;
  addText: string = '';
  result: number = -1;
  isNa: boolean = false;
  vldObj: any;

  constructor(
    private rankServ : RankingService,
    private services: SharedServices,
    private qTypesServ: QuestionTypesService,
    private valdServ: SurveyValidationService
  ) { }

  ngOnInit() {
    // console.log("completeQuestion.data.get('fieldOptions.col').value",this.completeQuestion.data.get('fieldOptions.col').value);
    let rowIdx = this.services.utils.findIndex(this.completeQuestion.data.get('fieldOptions.row').value, '_id', this.item.rowId);
    this.opt = this.completeQuestion.data.get('fieldOptions.row').at(rowIdx);
    this.columns = this.completeQuestion.data.get('fieldOptions.col');
    this.result = (this.item.res != -1) ? this.item.res : this.result;
    this.isNa = !!this.item.isNa;
    this.addText = this.item.rowText || '';
  }

  ngOnChanges(changes: SimpleChanges) {
    let rowIdx = this.services.utils.findIndex(this.completeQuestion.data.get('fieldOptions.row').value, '_id', this.item.rowId);
    this.opt = this.completeQuestion.data.get('fieldOptions.row').at(rowIdx);
    this.result = (this.item.res != -1) ? this.item.res : this.result;
    this.isNa = !!this.item.isNa;
    this.item.isHidden = this.opt.get('isHidden').value;
  }

  changeRank() {
    let isNa = (this.result == this.data.activeRowLen) ? true : false;
    this.isNa = isNa && this.columns.at(this.columns.length - 1).get('isNa').value ? true : false;
    this.updateAnswer(+this.result, this.isNa);
  }

  existsInRemaining(optIndex){
    let ret = true;
    if((this.rankServ.getRankedBag().indexOf(optIndex+1) != -1)
        && (this.rankServ.getRankOfRowId(this.item.rowId) != (optIndex+1)))
      ret = false;
    return ret;
  }

  setNaOpt() {
    this.result = -1;
    this.isNa && (this.result = this.data.activeRowLen);
    this.updateAnswer(this.result, this.isNa);
  }

  setAdditionalText() {
    if(this.addText)
      this.vldObj = this.valdServ.checkAdditionalTextValidation(this.opt.value, '', this.addText.trim());
      this.updateAnswer(this.result, this.isNa);
  }

  updateAnswer(option: number, isNa: boolean) {
    // console.log("option::",option)
    let addTxt = this.addText;
    let result: any = {rIdx: this.item.rowIdx, result: option, isNa: isNa, addText: addTxt};
    if(isNa) {
      result.text = this.columns.at(this.columns.length - 1).value.text;
    } else {
      result.text = (option + 1).toString();
    }
    this.rankServ.addOrRemove(this.item.rowId, option+1);
    this.onChange.emit(result);
  }

}



