import { Component, OnInit, Input } from '@angular/core';

@Component({
  selector: '[bindHtml]',
  template: `
    <span [innerHTML]="(bindHtml.value != '' ? (bindHtml.value | safeHtml) : (bindHtml.default ? bindHtml.default : ''))"></span>
  `
})
export class BindHtmlComponent implements OnInit {
  @Input() bindHtml;
  constructor() { }

  ngOnInit() {
  }

}



