import { Injectable } from '@angular/core';

@Injectable()
export class TinymceService {
  sepatetor: string = '|';
  buttons: any = {
    bold: 'bold',
    italic: 'italic',
    underline: 'underline',
    custom: {
      insertImage: 'insertImageButton',
      piping: 'pipingBtn'
    },
    advlist: 'advlist',
    anchor: 'anchor',
    autolink: 'autolink',
    autoresize: 'autoresize',
    autosave: 'autosave',
    bbcode: 'bbcode',
    charmap: 'charmap',
    code: 'code',
    codesample: 'codesample',
    colorpicker: 'colorpicker',
    contextmenu: 'contextmenu',
    directionality: 'directionality',
    emoticons: 'emoticons',
    fullpage: 'fullpage',
    fullscreen: 'fullscreen',
    help: 'help',
    hr: 'hr',
    image: 'image',
    imagetools: 'imagetools',
    importcss: 'importcss',
    insertdatetime: 'insertdatetime',
    legacyoutput: 'legacyoutput',
    link: 'link',
    lists: 'lists',
    media: 'media',
    nonbreaking: 'nonbreaking',
    noneditable: 'noneditable',
    pagebreak: 'pagebreak',
    paste: 'paste',
    preview: 'preview',
    print: 'print',
    save: 'save',
    searchreplace: 'searchreplace',
    spellchecker: 'spellchecker',
    tabfocus: 'tabfocus',
    table: 'table',
    template: 'template',
    textcolor: 'textcolor',
    textpattern: 'textpattern',
    toc: 'toc',
    visualblocks: 'visualblocks',
    visualchars: 'visualchars',
    wordcount: 'wordcount'
  }


  constructor(

  ) {}


  getAllControls() {
      let ctrlArray = [];
      for (let k in this.buttons) {
          if(k != 'custom') {
              ctrlArray.push(this.buttons[k]);
          }
      }
      return ctrlArray;
  }

  allButtons(): string {
    let btnArray = [];
    btnArray.push(this.buttons.bold);
    btnArray.push(this.buttons.italic);
    btnArray.push(this.sepatetor);
    btnArray.push(this.buttons.custom.insertImage);
    btnArray.push(this.sepatetor);
    btnArray.push(this.buttons.custom.piping);
    return btnArray.join(' ');
  }

  customButtons(buttons : any): string {
    let btnArray = [];
    buttons.forEach( (item) => {
        btnArray.push(item);
    })
    return btnArray.join(' ');
  }

}



