import { Injectable } from '@angular/core';

@Injectable()
export class ImageUploadService {
  pipingImage: any = {
    src: '',
    x: 0,
    y: 0,
    height: 0,
    width: 0,
    string: ''
  }

  constructor() { }

  initializePipingImgObj(e: any){
    this.pipingImage.src = e.url;
    this.pipingImage.x = -e.size.x;
    this.pipingImage.y = -e.size.y;
    this.pipingImage.height = e.size.height;
    this.pipingImage.width = e.size.width;
    let imgStyle = `
    background: url(${this.pipingImage.src}) ${this.pipingImage.x}px ${this.pipingImage.y}px no-repeat;height:${this.pipingImage.height}px; width:${this.pipingImage.width}px`;

    this.pipingImage.string = `<div style='overflow-y: hidden;overflow-x:auto' contenteditable='false'>
      <div class="imageZoomS2c" style="${imgStyle}" ></div>
    </div><p>&nbsp;</p>`;
  }

  getPipedImgElem(){
    return this.pipingImage.string;
  }

}



