import { Directive, ChangeDetectorRef, ElementRef, AfterViewInit, OnDestroy, Input, Output, EventEmitter } from '@angular/core';
import { TinymceService } from '../../services/tinymce.service';

tinymce.baseURL = "/assets/tinymce/js/tinymce";
@Directive({
  selector: '[appTinymceFull]'
})
export class TinymceFullDirective implements AfterViewInit, OnDestroy {
  @Input() element: any;
  @Output() onBlur = new EventEmitter < any > ();
  @Output() onEvent = new EventEmitter < any > ();

  editor;
  private id: string = Math.random().toString(36).substr(2, 5);

  constructor(
    el: ElementRef,
    private rdf: ChangeDetectorRef,
    private tinymceService: TinymceService
  ) {
    el.nativeElement.setAttribute('id', this.id);
  }

  ngAfterViewInit() {
    let style = "margin: 0; padding:0;";

    tinymce.init({
      selector: `#${this.id}`,
      toolbar_items_size: 'small',
      content_style: "body{margin: 2px;}",
      content_css: '/assets/skins/lightgray/content.min.css',
      min_height: 230,//(this.element.others && this.element.others.minHeight ? this.element.others.minHeight : 30),
      menubar: true,
      autoresize_min_height: 30,//(this.element.others && this.element.others.minHeight ? this.element.others.minHeight : 30),
      autoresize_bottom_margin: 0,
      autoresize_max_height: 150,//(this.element.others && this.element.others.maxHeight ? this.element.others.maxHeight : 150),
    //   directionality:this.element.others && this.element.others.dir || 'ltr',
    //   width: (this.element.others && this.element.others.width ? this.element.others.width : '100%'),
      statusbar: false,
      convert_fonts_to_spans: false,
      inline: false,
      plugins: [
		  'table advlist autolink lists link image charmap print preview hr anchor pagebreak',
            'searchreplace wordcount',
            'insertdatetime media contextmenu directionality',
            'emoticons paste textcolor colorpicker textpattern imagetools fullscreen noneditable'
      ],
      // forced_root_block : 'p',
      forced_root_block_attrs: {
        "style": style
      },
	  toolbar1: 'fontsizeselect fontselect underline undo redo | insert | styleselect | bold italic | alignleft aligncenter alignright alignjustify | bullist numlist outdent indent | link image',
        toolbar2: 'print preview fullscreen media | forecolor backcolor emoticons | codesample',
        fontsize_formats: '5px 8px 10px 12px 14px 16px 18px 20px 22px 24px 26px 28px 30px 32px 34px 36px 38px 40px 42px 44px 46px 48px 50px 52px 54px 56px 58px 60px 62px 66px 70px 76px',
      // toolbar: "bold italic underline | fontselect | insertImageButton | pipingBtn | advancedEditBtn",
	  font_formats: "Andale Mono=andale mono,times;" +
            "Arial=arial,helvetica,sans-serif;" +
            "Arial Black=arial black,avant garde;" +
            "Book Antiqua=book antiqua,palatino;" +
            "Comic Sans MS=comic sans ms,sans-serif;" +
            "Courier New=courier new,courier;" +
            "Georgia=georgia,palatino;" +
            "Helvetica=helvetica;" +
            "Impact=impact,chicago;" +
            "Symbol=symbol;" +
            "Tahoma=tahoma,arial,helvetica,sans-serif;" +
            "Terminal=terminal,monaco;" +
            "Times New Roman=times new roman,times;" +
            "Trebuchet MS=trebuchet ms,geneva;" +
            "Verdana=verdana,geneva;" +
            "Webdings=webdings;" +
            "Wingdings=wingdings,zapf dingbats;" +
            "Lobster=Lobster,cursive;" +
            "Bungee Hairline=Bungee Hairline,cursive;" +
            "Shrikhand=Shrikhand,cursive",
      skin_url: '/assets/skins/lightgray',
      formats : {
        underline : {inline : 'u', exact : true}
      },
      contextmenu: "cut copy paste",
      toolbar_item_size: "small",

      setup: editor =>  {
          let that = this;
          this.editor = editor;
        //   editor.addButton('pipingBtn', {
        //     text: '+ Piping',
        //     tooltip: 'Insert Piping',
        //     classes: 'insert-piping-btn',
        //     onclick: () => {
        //       this.onEvent.emit({type: 'btnClick', options: { action: 'piping'}});
        //     }
        //   }),
        //   editor.addButton('insertImageButton', {
        //     icon: 'insert-img-in-tinymce',
        //     tooltip: 'Upload Image',
        //     classes: 'image-upload-btn',
        //     onclick: () => {
        //       this.onEvent.emit({type: 'btnClick', options: { action: 'imgUpload'}});
        //     }
        //   }),
          // editor.addButton('videoBtn', {
          //   text: '+ Piping',
          //   tooltip: 'Insert Piping',
          //   classes: 'insert-piping-btn',
          //   onclick: () => {
          //     this.onEvent.emit({type: 'btnClick', options: { action: 'image'}});
          //   }
          // }),
        //   editor.addButton('advancedEditBtn', {
        //     tooltip: 'Advanced Editor',
        //     classes: 'adv-edit-btn',
        //     onclick: function() {
        //       console.log('advanced editor');
        //     }
        //   }),
          editor.on("init", () => {
            // editor.getContainer().querySelector('.mce-toolbar-grp').classList.add('focus-tinymce');
            // editor.getContainer().querySelector('.mce-edit-area').classList.add(this.element.className);
            editor.getBody().style.minHeight = '0px';
			editor.getContainer().querySelector('.mce-toolbar-grp').style.display = 'block';
            // if(this.element.isNew == true){
            //   editor.focus();
            // }
          });
          editor.on('focus', () => {
            // if(this.element.others.display == undefined || this.element.others.display){
            // }
              editor.getContainer().querySelector('.mce-toolbar-grp').style.display = 'block';
            // editor.selection.select(editor.getBody(), true);
            // editor.selection.collapse(false);
          });
          editor.on('blur', () => {
            // const content = editor.getContent();
            // editor.getContainer().querySelector('.mce-toolbar-grp').style.display = 'none';
            // this.onBlur.emit({content: content, element: this.element['text']});
          });
          editor.on('keyup', (e) => {
            // const content = editor.getContent();
            // this.onEvent.emit({type: 'keyup', options: { content: content, element: this.element['text'] }});
            // this.rdf.detectChanges();
          });
          editor.on('keydown', (e) => {
            // if(this.element.className == 'tinyMceOptionNoLeftPadding' || this.element.className == 'tinyMceOption'){
            //   if(e.which == 13){
            //     e.stopPropagation();
            //     this.onEvent.emit({type: 'keyEnter', options: this.element.others.options});
            //     return false;
            //   }
            // }
          });
          editor.on('change', () => {
            // const content = editor.getContent();
            // this.onEvent.emit({type: 'keyup', options: { content: content, element: this.element['text'] }})
            // this.rdf.detectChanges();
          });
      }
    });
  }

  ngOnDestroy() {
    tinymce.remove(this.editor);
  }

}



