import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { FormGroup} from '@angular/forms';
import { UtilsService } from '../../services/utils.service';
import { SurveyValidationService } from '../../services/validation.module';

@Component({
  selector: 'app-single-textbox',
  templateUrl: './single-textbox.component.html',
  styleUrls: ['../question-templates.scss', './single-textbox.component.scss']
})
export class SingleTextboxComponent implements OnInit {
  @Input() questionData: any;
  @Input() responseData?: any;
  @Input() langDirection: string;
  @Input() optTheme: any;
  @Output() onChange: EventEmitter<any> = new EventEmitter<any>();
  completeQuestion: any;
  text: string;
  vldObj: any ={ret: true, msg: ""};

  constructor(
    private valdServ: SurveyValidationService,
    private utils: UtilsService
  ) { }

  ngOnInit() {
    this.completeQuestion = this.questionData;
    this.responseData && this.setResponse();
  }

  setResponse() {
    this.responseData.forEach(item=>{
      this.text = item.text;
    });
    this.onSelectionChange();
  }

  onSelectionChange() {
    let qId = this.completeQuestion.data.get('_id').value;
    let ansData = [];
    if(this.text)
      this.vldObj = this.valdServ.checkValidation(this.completeQuestion.data.get('fieldOptions.row').value[0], '', this.text.trim());

    if(this.text && this.vldObj && this.vldObj.ret){
      ansData = [{rowId: this.completeQuestion.data.get('fieldOptions.row').value[0]._id, text: this.text}];
    }

    this.onChange.emit({
      qId: qId,
      ansData: ansData,
      validPresent: (this.text && this.vldObj.msg),
      validated: (this.text && this.vldObj.ret),
      addtionalTextPresent: false,
      additionaTextValidationPresent: false,
      additionaTextValidated: false,
      completedResp: (ansData.length >= 1)
    });
  }

  getText(str: string){
    return this.utils.getTextOutOfHtml(str);
  }

}



