import { Injectable } from '@angular/core';
import { UtilsService } from '../../../shared/services/utils.service';
import { QuestionTypesService } from '../../../shared/questionTemplates/question-types.service';

@Injectable()
export class EditConstantsService {
  private readonly rowCol: any = {
    text: '',
    weight: 0,
    isComment: 0,
    hasText: 0,
    isNa: 0,
    isHidden: 0,
    color: '#2392ED',
    items: [],
    validation: {
      type: "", //new type of validations: date, number, text
      subType:"", //new sub-type of validations: number -> decimalNumber/wholeNumber/naturalNumber/Integer, text: normalText/email, date: withTime/withoutTime
      family: "", // new family of type : 'individual'/'rowOnly'/'columnOnly'/'all'
      text: "", //Error message
      max: "",
      min: "",
      valdType: ""
    },
    addnTextvalidation: {
      type: "", //new type of validations: date, number, text
      subType:"", //new sub-type of validations: number -> decimalNumber/wholeNumber/naturalNumber/Integer, text: normalText/email, date: withTime/withoutTime
      family: "", // new family of type : 'individual'/'rowOnly'/'columnOnly'/'all'
      text: "", //Error message
      max: "",
      min: "",
      valdType: ""
    },
    _id: ''
  };
  private readonly pipingObj: any = {
    pipedQuestions: [],
    hasPiping: 0
  };
  private readonly baseQuesProp: any = {
    title: '',
    required: true,
    reqMsg: 'This question requires an answer',
    piping: this.pipingObj,
    surId: '',
    _id: ''
  };
  allQuesTypes: any;
  allQues: any;

  constructor(
    private utils: UtilsService,
    private quesTypes: QuestionTypesService
  ) {

    this.allQuesTypes = this.quesTypes.questionTypesJSON;

    this.allQues = {
      'single-choice': this.utils.extendObject(this.baseQuesProp, {
        fieldType: {
          family: this.allQuesTypes['single-choice'].family,
          subtype: this.allQuesTypes['single-choice'].subtype && this.allQuesTypes['single-choice'].subtype.split(',')[0]
        },
        fieldOptions: {
          row: this.getRowCol(2),
        },
        properties: {
          align: 'vertical'
        }
      }),
      'mul-choice': this.utils.extendObject(this.baseQuesProp, {
        fieldType: {
          family: this.allQuesTypes['mul-choice'].family,
          subtype: this.allQuesTypes['mul-choice'].subtype && this.allQuesTypes['mul-choice'].subtype.split(',')[0]
        },
        fieldOptions: {
          row: this.getRowCol(2),
        },
        properties: {
          align: 'vertical'
        }
      }),
      'nps': this.utils.extendObject(this.baseQuesProp, {
        fieldType: {
          family: this.allQuesTypes['nps'].family,
          subtype: this.allQuesTypes['nps'].subtype && this.allQuesTypes['nps'].subtype.split(',')[0]
        },
        fieldOptions: {
          row: this.getRowCol(1),
          col: this.getRowCol(11)
        },
        properties: {
          nps: {
            differentView: false,
            desktop: {
              type: Object.keys(this.quesTypes.questionTypesRelatedData['nps'])[0],
              dColor: true
            },
            mobile: {
              type: Object.keys(this.quesTypes.questionTypesRelatedData['nps'])[0],
              mColor: true
            }
          }
        }
      }),
      'single-text': this.utils.extendObject(this.baseQuesProp, {
        fieldType: {
          family: this.allQuesTypes['single-text'].family,
          subtype: this.allQuesTypes['single-text'].subtype && this.allQuesTypes['single-text'].subtype.split(',')[0]
        },
        fieldOptions: {
          row: this.getRowCol(1)
        }
      }),
      'dropdown': this.utils.extendObject(this.baseQuesProp, {
        fieldType: {
          family: this.allQuesTypes['dropdown'].family,
          subtype: this.allQuesTypes['dropdown'].subtype && this.allQuesTypes['dropdown'].subtype.split(',')[0]
        },
        fieldOptions: {
          row: this.getRowCol(2)
        },
        properties: {
          order: 'default'
        }
      }),
      'ranking': this.utils.extendObject(this.baseQuesProp, {
        fieldType: {
          family: this.allQuesTypes['ranking'].family,
          subtype: this.allQuesTypes['ranking'].subtype && this.allQuesTypes['ranking'].subtype.split(',')[0]
        },
        fieldOptions: {
          row: this.getRowCol(2),
          col: this.getRowCol(2)
        }
      }),
      'mul-text': this.utils.extendObject(this.baseQuesProp, {
        fieldType: {
          family: this.allQuesTypes['mul-text'].family,
          subtype: this.allQuesTypes['mul-text'].subtype && this.allQuesTypes['mul-text'].subtype.split(',')[0]
        },
        fieldOptions: {
          row: this.getRowCol(2)
        },
        validation:{
          valdType: "",
          max: 0,
          min: 0,
          text: ""
        }
      }),
      'matrix-rating': this.utils.extendObject(this.baseQuesProp, {
        fieldType: {
          family: this.allQuesTypes['matrix-rating'].family,
          subtype: this.allQuesTypes['matrix-rating'].subtype && this.allQuesTypes['matrix-rating'].subtype.split(',')[0]
        },
        fieldOptions: {
          row: this.getRowCol(2),
          col: this.getRowCol(2)
        },
        properties: {
          forceDesktopView: false
        }
      }),
      'matrix-dropdown': this.utils.extendObject(this.baseQuesProp, {
        fieldType: {
          family: this.allQuesTypes['matrix-dropdown'].family,
          subtype: this.allQuesTypes['matrix-dropdown'].subtype && this.allQuesTypes['matrix-dropdown'].subtype.split(',')[0]
        },
        fieldOptions: {
          row: this.getRowCol(2),
          col: this.getRowCol(2)
        },
        properties: {
          forceDesktopView: false
        }
      }),
      'matrix-text': this.utils.extendObject(this.baseQuesProp, {
        fieldType: {
          family: this.allQuesTypes['matrix-text'].family,
          subtype: this.allQuesTypes['matrix-text'].subtype && this.allQuesTypes['matrix-text'].subtype.split(',')[0]
        },
        fieldOptions: {
          row: this.getRowCol(2),
          col: this.getRowCol(2)
        },
        properties: {
          forceDesktopView: false
        }
      }),
      'graph-rating': this.utils.extendObject(this.baseQuesProp, {
        fieldType: {
          family: this.allQuesTypes['graph-rating'].family,
          subtype: this.allQuesTypes['graph-rating'].subtype && this.allQuesTypes['graph-rating'].subtype.split(',')[0]
        },
        fieldOptions: {
          row: this.getRowCol(2),
          col: this.getRowCol(3)
        },
        properties: {
          allowColLabels: false,
          allowRowLabels: false,
          colLabelsTop: true
        }
      }),
      'contacts': this.utils.extendObject(this.baseQuesProp, {
        fieldType: {
          family: this.allQuesTypes['contacts'].family,
          subtype: this.allQuesTypes['contacts'].subtype && this.allQuesTypes['contacts'].subtype.split(',')[0]
        },
        fieldOptions: {
          row: this.getRowCol(Object.keys(this.quesTypes.questionTypesRelatedData['contactsJSON']).length)
        }
      }),
      'comment': this.utils.extendObject(this.baseQuesProp, {
        fieldType: {
          family: this.allQuesTypes['comment'].family,
          subtype: this.allQuesTypes['comment'].subtype && this.allQuesTypes['comment'].subtype.split(',')[0]
        },
        fieldOptions: {
          row: this.getRowCol(1)
        }
      }),
      'statement': this.utils.extendObject(this.baseQuesProp, {
        fieldType: {
          family: this.allQuesTypes['statement'].family,
          subtype: this.allQuesTypes['statement'].subtype && this.allQuesTypes['statement'].subtype.split(',')[0]
        },
        fieldOptions: {
        }
      }),
      'date-time': this.utils.extendObject({
        title: '',
        required: true,
        reqMsg: 'This question requires an answer',
        piping: this.pipingObj,
        validation: {
          text: 'date_intl'
        },
        surId: '',
        _id: ''
      } ,
      {
        fieldType: {
          family: this.allQuesTypes['date-time'].family,
          subtype: this.allQuesTypes['date-time'].subtype && this.allQuesTypes['date-time'].subtype.split(',')[0]
        },
        fieldOptions: {
          row: this.getRowCol(1)
        }
      }),
      'file': this.utils.extendObject(this.baseQuesProp, {
        fieldType: {
          family: this.allQuesTypes['file'].family,
          subtype: this.allQuesTypes['file'].subtype && this.allQuesTypes['file'].subtype.split(',')[0]
        },
        fieldOptions: {
          upload: [{
            fieldVald: {
              fileType: Object.keys(this.quesTypes.questionTypesRelatedData['fileUploadJSON'])[0],
              format: this.quesTypes.questionTypesRelatedData['fileUploadJSON'][Object.keys(this.quesTypes.questionTypesRelatedData['fileUploadJSON'])[0]].format,
              maxFileSize: this.quesTypes.questionTypesRelatedData['fileUploadSizeJSON'][0]
            }
          }],
          uploadType: 'answerOpt'
        }
      }),
      'slider': this.utils.extendObject(this.baseQuesProp, {
        fieldType: {
          family: this.allQuesTypes['slider'].family,
          subtype: this.allQuesTypes['slider'].subtype && this.allQuesTypes['slider'].subtype.split(',')[0]
        },
        fieldOptions: {
          row: this.getRowCol(1),
          col: this.getRowCol(1)
        },
        properties: {
          valType: 'continous',
          minValue: '0',
          maxValue: '100',
          step: '1',
          start: '50',
          minLabel: 'Min',
          maxLabel: 'Max',
          showInput: false
        }
      }),
    }
    this.allQues['nps'].fieldOptions.col.forEach((val, idx) => {
      val.text = String(idx);
    })
    this.allQues['contacts'].fieldOptions.row.forEach((val, idx) => {
      val.text = this.quesTypes.questionTypesRelatedData['contactsJSON'][idx].text
    })
    this.allQues['graph-rating'].fieldOptions.row.forEach((val) => {
        val.optType = {};
        val.optType.family = Object.keys(this.quesTypes.questionTypesRelatedData['graphicalRatingTypeJSON'])[0];
        val.optType.subtype = val.optType.family;
    });
    this.allQues['ranking'].fieldOptions.col.forEach((val, idx) => {
      val.text = String(idx + 1);
    })
    this.allQues['slider'].fieldOptions.row.forEach((val) => {
        val.optType = {};
        val.optType.family = 'slider';
        val.optType.subtype = 'type1';
    });
  }

  getRowCol(len: number){
    let arr: Array<any> = [];
    let idx = 0;
    while(idx < len){
      arr.push(this.utils.cloneObject(this.rowCol));
      arr[idx]._id = this.utils.getUnique4lengthStr();
      idx++;
    }
    return arr;
  }

}



