import { Component, OnInit, OnChanges, SimpleChanges, DoCheck, Input, Output, EventEmitter } from '@angular/core';
import { FormGroup, FormArray} from '@angular/forms';

import { QuestionTypesService } from '../question-types.service';
import { SharedServices } from '../../services/services.module';
import { SurveyValidationService } from '../../services/validation.module';

@Component({
  selector: 'app-matrix-textbox',
  templateUrl: './matrix-textbox.component.html',
  styleUrls: ['../question-templates.scss', './matrix-textbox.component.scss']
})
export class MatrixTextboxComponent implements OnInit, OnChanges, DoCheck {
  @Input() questionData: any;
  @Input() changesInChild : any;
  @Input() responseData: any;
  @Input() langDirection: string;
  @Input() optTheme: any;
  @Output() onChange: EventEmitter<any> = new EventEmitter<any>();
  completeQuestion: any;
  quesToDisplay: any = {
    rows: [],
    cols: []
  }
  rowLen: number
  ansData: Array<any> = [];
  textBoxs = {
    comment: {},
    addText: {
      col: {},
      row: {}
    },
    option: {}
  };
  vldObjRow: Array<any> = [];
  vldObjCol: Array<any> = [];
  vldObjInp: Array<any> = [];

  constructor(
    private services: SharedServices,
    private qTypesServ: QuestionTypesService,
    private valdServ: SurveyValidationService
  ) { }

  ngOnInit() {
    this.completeQuestion = this.questionData;
    this.setQuestionToBeDisplayed();
    this.rowLen = (<FormArray>this.completeQuestion.data.get('fieldOptions.row')).length;
    this.completeQuestion.data.get('fieldOptions.row').value.forEach((item, count)=> {
      this.textBoxs.option[item._id]={};
      this.textBoxs.comment[item._id]={}
    });
    this.responseData && this.setResponse();
  }

  ngOnChanges(changes: SimpleChanges) {
    if(this.completeQuestion){
      this.setQuestionToBeDisplayed();
    }
  }

  setQuestionToBeDisplayed(){
    this.quesToDisplay.rows = this.completeQuestion.data.get('fieldOptions.row').controls.filter(opt => opt.get('isHidden').value == 0);
    this.quesToDisplay.cols = this.completeQuestion.data.get('fieldOptions.col').controls.filter(opt => opt.get('isHidden').value == 0);
  }

  setResponse() {
    this.ansData = this.responseData;
    this.responseData.forEach(item=>{
      if(item.colId) {
        if(this.checkIfCommentIsColumn(item.colId))
          this.textBoxs.option[item.rowId][item.colId] = item.colText;
        else
          this.textBoxs.option[item.rowId][item.colId] = item.text;

        item.rowText && (this.textBoxs.addText.row[item.rowId] = item.rowText);
        item.colText && (this.textBoxs.addText.col[item.colId] = item.colText);
      } else {
        this.textBoxs.comment[item.rowId][0] = item.text;
      }
    });
  }

  checkIfCommentIsColumn(colId){
    let ret = false;
    for(let i=0; i<this.quesToDisplay.cols.length; i++){
      if(this.quesToDisplay.cols[i].get('isComment').value && this.quesToDisplay.cols[i].get('_id').value == colId){
        ret = true;
        break;
      }
    }
    return ret;
  }

  ngDoCheck(){
    this.handleForNgModelUndefined();
  }

  handleForNgModelUndefined() {
    let currRowLen = (<FormArray>this.completeQuestion.data.get('fieldOptions.row')).length;
    if(this.rowLen < currRowLen){
      this.completeQuestion.data.get('fieldOptions.row').value.forEach((item, count)=> {
        this.textBoxs.option[item._id]=this.textBoxs.option[item._id] || {};
        this.textBoxs.comment[item._id]=this.textBoxs.comment[item._id] || {}
      });
      this.rowLen = currRowLen;
    }
  }

  setAddRowText(rowId: string) {
    if(this.textBoxs.addText.row[rowId])
      this.vldObjRow[rowId] = this.valdServ.checkAdditionalTextValidation(this.services.form.formObj.getQuestionById(this.completeQuestion.data.get('_id').value).getOptionById(rowId), '', this.textBoxs.addText.row[rowId].trim());

    if(this.vldObjRow[rowId] && this.vldObjRow[rowId].ret){
      this.ansData.filter(item=>item.rowId==rowId).forEach(item=>item.rowText=this.textBoxs.addText.row[rowId]);
    }
    this.updateResponse();
  }

  setAddColText(colId: string) {
    if(this.textBoxs.addText.col[colId])
      this.vldObjCol[colId] = this.valdServ.checkAdditionalTextValidation('', this.services.form.formObj.getQuestionById(this.completeQuestion.data.get('_id').value).getOptionById(colId), this.textBoxs.addText.col[colId].trim());

    if(this.vldObjCol[colId] && this.vldObjCol[colId].ret){
      this.ansData.filter(item=>item.colId==colId).forEach(item=>item.colText=this.textBoxs.addText.col[colId]);
    }
    this.updateResponse();
  }

  setOption(rowId: string, colId: string, isColAddntText) {
    this.ansData = this.ansData.filter(item=>!((item.rowId==rowId) && (item.colId==colId)));
    let text = this.textBoxs.option[rowId][colId];
    let data;

    if(!isColAddntText && text){
      this.vldObjInp[rowId+'-'+colId] = this.valdServ.checkValidation(this.services.form.formObj.getQuestionById(this.completeQuestion.data.get('_id').value).getOptionById(rowId), this.services.form.formObj.getQuestionById(this.completeQuestion.data.get('_id').value).getOptionById(colId), text.trim());
    }else if(isColAddntText && text){
      this.vldObjCol[rowId+'-'+colId] = this.valdServ.checkAdditionalTextValidation(this.services.form.formObj.getQuestionById(this.completeQuestion.data.get('_id').value).getOptionById(rowId), this.services.form.formObj.getQuestionById(this.completeQuestion.data.get('_id').value).getOptionById(colId), text.trim());
    }

    if(text && ((isColAddntText && this.vldObjCol[rowId+'-'+colId] && this.vldObjCol[rowId+'-'+colId].ret) || (!isColAddntText && this.vldObjInp[rowId+'-'+colId] && this.vldObjInp[rowId+'-'+colId].ret))) {
      data = {rowId: rowId, colId: colId};
      if(!isColAddntText){
        data['text'] = text;
        this.textBoxs.addText.row[rowId] && (data['rowText'] = this.textBoxs.addText.row[rowId]);
        this.textBoxs.addText.col[colId] && (data['colText'] = this.textBoxs.addText.col[colId]);
      }else{
        data['colText'] = text;
        this.textBoxs.addText.row[rowId] && (data['rowText'] = this.textBoxs.addText.row[rowId]);
      }
    }
    this.updateResponse(data);
  }

  setComment(rowId: string) {
    this.ansData = this.ansData.filter(item=>item.rowId!=rowId);
    let text = this.textBoxs.comment[rowId][0];
    let data;

    if(text)
      this.vldObjRow[rowId] = this.valdServ.checkAdditionalTextValidation(this.services.form.formObj.getQuestionById(this.completeQuestion.data.get('_id').value).getOptionById(rowId), '', text.trim());

    if(text && (this.vldObjRow[rowId] && this.vldObjRow[rowId].ret)) {
      data = {rowId: rowId, text: text};
      this.textBoxs.addText.row[rowId] && (data['rowText'] = this.textBoxs.addText.row[rowId]);
    }
    this.updateResponse(data);
  }

  updateResponse(data?: any) {
    data && this.ansData.push(data);
    console.log(this.ansData);

    let qId = this.completeQuestion.data.get('_id').value;

    let completenessRow = false, completenessCol = false;
    for(let rowix=0; rowix<this.quesToDisplay.rows.length; rowix++){
      //console.log("roww",this.quesToDisplay.rows[rowix])

        if(this.checkCommentExistsAsRow(rowix)){
          //console.log("comm",this.quesToDisplay.rows[rowix].get('isComment').value)
          completenessRow = true;
        }else if(this.quesToDisplay.rows[rowix].get('isComment').value == 0){
          if(this.ifExistsRowIxInAnsData(rowix)){
            completenessRow = true;
          }else{
            completenessRow = false;
          }
        }else{
          completenessRow = false;
        }


      if(!completenessRow)
        break;
    }

// console.log("this.quesToDisplay.cols",this.quesToDisplay.cols);
    for(let colix=0; colix<this.quesToDisplay.cols.length; colix++){
      // console.log("roww",this.quesToDisplay.cols[colix])
      // console.log("comm",this.textBoxs.comment[this.quesToDisplay.cols[colix].get('_id').value])
      if(this.quesToDisplay.cols[colix].get('isComment').value){
        console.log("comm",this.quesToDisplay.cols[colix].get('isComment').value)
        if(this.checkCommentExistsAsCol(this.quesToDisplay.cols[colix].get('_id').value))
          completenessCol = true;
        else
          completenessCol = false;
      }else if(this.quesToDisplay.cols[colix].get('isComment').value == 0){
        if(this.ifExistsColIxInAnsData(colix)){
          completenessCol = true;
        }else{
          completenessCol = false;
        }
      }else{
        completenessCol = false;
      }

      if(!completenessCol)
        break;
    }

    let addTxtPst = false;
    let addTxtValdPst = false;
    let addTxtValdtd = true;
    for(let roi =0; roi < this.quesToDisplay.rows.length; roi++){
      addTxtPst = this.services.form.formObj.getQuestionById(qId).getOptionById(this.quesToDisplay.rows[roi].get('_id').value).hasText
      if(addTxtPst)
        break;
    }

    if(!addTxtPst){
      for(let coi =0; coi < this.quesToDisplay.cols.length; coi++){
        addTxtPst = this.services.form.formObj.getQuestionById(qId).getOptionById(this.quesToDisplay.cols[coi].get('_id').value).hasText
        if(addTxtPst)
          break;
      }
    }

    for(let roi =0; roi < this.quesToDisplay.rows.length; roi++){
      if(this.textBoxs.addText.row[this.quesToDisplay.rows[roi].get('_id').value]
          && !this.quesToDisplay.rows[roi].get('isComment').value
          && this.vldObjRow[this.quesToDisplay.rows[roi].get('_id').value].msg){
        addTxtValdPst = true;
        break;
      }
    }

    if(!addTxtValdPst){
      for(let coi =0; coi < this.quesToDisplay.cols.length; coi++){
        if(this.textBoxs.addText.col[this.quesToDisplay.cols[coi].get('_id').value]
          && this.vldObjCol[this.quesToDisplay.cols[coi].get('_id').value].msg){
          addTxtValdPst = true;
          break;
        }
      }
    }

    for(let roi =0; roi < this.quesToDisplay.rows.length; roi++){
      if(this.textBoxs.addText.row[this.quesToDisplay.rows[roi].get('_id').value]
         && !this.quesToDisplay.rows[roi].get('isComment').value
         && !this.vldObjRow[this.quesToDisplay.rows[roi].get('_id').value].ret){
        addTxtValdtd = false;
        break;
      }
    }

    if(addTxtValdtd){
      for(let coi =0; coi < this.quesToDisplay.cols.length; coi++){
        if(this.textBoxs.addText.col[this.quesToDisplay.cols[coi].get('_id').value]
          && !this.vldObjCol[this.quesToDisplay.cols[coi].get('_id').value].ret){
          addTxtValdtd = false;
          break;
        }
      }
    }

    let inputValidationPresent = false;

    for(let colix=0; colix<this.quesToDisplay.cols.length; colix++){
      for(let rowix=0; rowix<this.quesToDisplay.rows.length; rowix++){
        if(this.textBoxs.option[this.quesToDisplay.rows[rowix].get('_id').value][this.quesToDisplay.cols[colix].get('_id').value]
          && this.vldObjInp[this.quesToDisplay.rows[rowix].get('_id').value+"-"+this.quesToDisplay.cols[colix].get('_id').value]
          && this.vldObjInp[this.quesToDisplay.rows[rowix].get('_id').value+"-"+this.quesToDisplay.cols[colix].get('_id').value].msg){
          inputValidationPresent = true;
          break;
        }
      }
    }

    let inputFieldValdCompleted = true;

    for(let colix=0; colix<this.quesToDisplay.cols.length; colix++){
      for(let rowix=0; rowix<this.quesToDisplay.rows.length; rowix++){
        if(this.textBoxs.option[this.quesToDisplay.rows[rowix].get('_id').value][this.quesToDisplay.cols[colix].get('_id').value]
          && this.vldObjInp[this.quesToDisplay.rows[rowix].get('_id').value+"-"+this.quesToDisplay.cols[colix].get('_id').value]
          && !this.vldObjInp[this.quesToDisplay.rows[rowix].get('_id').value+"-"+this.quesToDisplay.cols[colix].get('_id').value].ret){
          inputFieldValdCompleted = false;
        }
      }
    }

    this.onChange.emit({
      qId: qId,
      ansData: this.ansData,
      validPresent: inputValidationPresent,
      validated: inputFieldValdCompleted,
      addtionalTextPresent: addTxtPst,
      additionaTextValidationPresent: addTxtValdPst,
      additionaTextValidated: addTxtValdtd,
      completedResp: (completenessCol && completenessRow)
    });
  }

  checkCommentExistsAsRow(rowix){
    if(this.quesToDisplay.rows[rowix].get('isComment').value && this.textBoxs.comment[this.quesToDisplay.rows[rowix].get('_id').value][0]
      && this.vldObjRow[this.quesToDisplay.rows[rowix].get('_id').value] && this.vldObjRow[this.quesToDisplay.rows[rowix].get('_id').value].ret)
      return true;
    else if(this.quesToDisplay.rows[rowix].get('isComment').value && !this.textBoxs.comment[this.quesToDisplay.rows[rowix].get('_id').value][0]){
      for(let ansix = 0; ansix < this.ansData.length; ansix++){
        console.log("this.ansData[ansix]",this.ansData[ansix])
        if((this.ansData[ansix].rowId != this.quesToDisplay.rows[rowix].get('_id').value)
          && !this.services.form.formObj.getQuestionById(this.completeQuestion.data.get('_id').value).getOptionById(this.ansData[ansix].rowId).isComment){
          return true;
        }
      }
      return false;
    }else{
      return false;
    }
  }

  checkCommentExistsAsCol(colId){
    console.log("vldObjCol",this.vldObjCol);
    let flag = false;
    for(let i=0; i<this.quesToDisplay.rows.length; i++){
      if(this.textBoxs.option[this.quesToDisplay.rows[i].get('_id').value][colId] && this.vldObjCol[this.quesToDisplay.rows[i].get('_id').value+'-'+colId]
        && this.vldObjCol[this.quesToDisplay.rows[i].get('_id').value+'-'+colId].ret){
        flag = true;
      }
      else if(!this.textBoxs.option[this.quesToDisplay.rows[i].get('_id').value][colId]){
        for(let ansix = 0; ansix < this.ansData.length; ansix++){
          console.log("this.ansData[ansix]",this.ansData[ansix])
          if((this.ansData[ansix].colId) && (this.ansData[ansix].colId != colId)
            && !this.services.form.formObj.getQuestionById(this.completeQuestion.data.get('_id').value).getOptionById(colId).isComment){
            flag = true;
            break;
          }
        }
      }else{
        flag = false;
      }

      if(!flag)
        break;
    }
    return flag;
  }

  ifExistsRowIxInAnsData(rowix){
    for(let ansix = 0; ansix < this.ansData.length; ansix++){
      //console.log("this.ansData[ansix]",this.ansData[ansix])
      if(this.ansData[ansix].text && this.ansData[ansix].rowId == this.quesToDisplay.rows[rowix].get('_id').value){
        return true;
      }
    }
    return false;
  }

  ifExistsColIxInAnsData(colix){
    let flagAllRowsAreComments = true;
    for(let i=0; i<this.quesToDisplay.rows.length; i++){
      if(!this.quesToDisplay.rows[i].get('isComment').value){
        flagAllRowsAreComments = false;
      }
    }

    if(flagAllRowsAreComments)
      return true;

    for(let ansix = 0; ansix < this.ansData.length; ansix++){
      //console.log("this.ansData[ansix]",this.ansData[ansix])
      if(this.ansData[ansix].text && this.ansData[ansix].colId == this.quesToDisplay.cols[colix].get('_id').value){
        return true;
      }
    }
    return false;
  }

  getCombinedStyle(arr){
    return this.services.utils.mergeObjs(arr);
  }

}



