import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';

import { QuestionTypesService } from '../questionTemplates/question-types.service';
import { UtilsService } from './utils.service';
import { AppResourceService } from './resource.service';
import { EditConstantsService } from '../../routes/survey/edit/edit.constants.service';

declare var Form;

@Injectable()
export class FormService {
  // private formData: any;
  private sId: string;
  public formObj: any;
  private allPagesAndQuestions: object = { data: null };
  private pipingObj: any;
  private isPipedObj: any;
  private questionCountAndId: object = { data: null };
  allQuestionsInFormGroup: any = {};
  allQuestionsOfSurvey: any = {};

  private tempSurveyData: BehaviorSubject<any> = new BehaviorSubject(null);
  private isSurveyCallStart: boolean = false;


  constructor(
    private questionTypesService: QuestionTypesService,
    private utils: UtilsService,
    private editConstants: EditConstantsService,
    private resources: AppResourceService
  ) { }


  getSurveyData(sId?: string): Observable<any> {
    this.setSurveyData(sId);
    return this.tempSurveyData.asObservable();
  }

  setSurveyData(sId?: string) {
    if(!this.isSurveyCallStart && !this.getFormData()) {
      this.isSurveyCallStart = true;
      if(sId){
        this.sId = sId;
      }
      this.fetchSurveyDataFromServer(sId).subscribe(data => {

      });
    } else if (this.getFormData()){
      this.tempSurveyData.next(this.getFormData());
    }
  }

  fetchSurveyDataFromServer(sId: string) {
    return this.resources.getSurveyById(sId).map(data=> {
      this.isSurveyCallStart = false;
      this.setFormData(data.form);
      this.convertAllQuestionsInFormGroup();
      this.tempSurveyData.next(this.getFormData());
      return data;
    });
  }

  setFormData(data: any) {
    this.formObj = new Form(data);
    this.setPipingObject();
    this.setAllPagesAndQuestions();
  }

  getFormData() {
    return this.formObj;
  }

  convertAllQuestionsInFormGroup(){
    this.formObj.getPageFields().forEach((page) => {
        page.getAllQuestions().forEach((ques) => {
          this.addNewQuesInAllQuesObj(ques);
        });
    });
  }

  addNewQuesInAllQuesObj(ques: any){
    this.allQuestionsOfSurvey[ques._id] = ques;
    let questype = this.questionTypesService.getQuestionType(ques.fieldType.family, ques.fieldType.subtype);
    let obj = this.utils.extendObjectValue(ques, this.editConstants.allQues[questype.type]);
    this.allQuestionsInFormGroup[ques._id] = {
      'data': {'data': this.questionTypesService.convertQuestion(obj)},
      'type': questype
    };
  }

  getQuestionsAfter(qId: String) {
    let qList: Array<any> = [];
    let flag: boolean = false;
    let count: number = 0;
    this.formObj.pageFields.forEach((page) => {
      if(page.formFields){
        page.formFields.forEach((ques) => {
          count++;
          if (flag) {
            let question = {
              count: count,
              _id: ques._id,
              title: ques.title
            }
            qList.push(question)
          }
          if (ques._id == qId) { flag = true }
        });
      }
    });
    return qList;
  }

  getAllQuestions(): Array<any> {
    let qList: Array<any> = [];
    let pCount: number = 0;
    let qCount: number = 0;
    this.formObj.pageFields.forEach((formPage) => {
      pCount++;
      let page = {
        count: pCount,
        title: "Page " + pCount,
        type: "p"
      }
      qList.push(page);
      if(formPage.formFields){
        formPage.formFields.forEach((ques) => {
          if (ques.fieldType.family != 'statement') {
            qCount++;
          }
          let question = {
            count: qCount,
            _id: ques._id,
            title: (ques.fieldType.family != 'statement' ? qCount + '. ' : '') + ques.title,
            type: (ques.fieldType.family == 'statement' ? 's' : 'q'),
            qType: this.questionTypesService.getQuestionType(ques.fieldType.family, ques.fieldType.subtype),
            options: ques.fieldOptions
          }
          qList.push(question);
        });
      }
    });
    return qList;
  }

  getAllPagesAndQuestions() {
    return this.allPagesAndQuestions;
  }

  setAllPagesAndQuestions(): any {
    this.allPagesAndQuestions['data'] = {};
    let pCount: number = 0;
    let qCount: number = 0;
    let qCountInPage: number = 0;
    let pageFieldsLength: number = this.formObj.pageFields.length;
    this.formObj.pageFields.forEach((formPage) => {
      qCountInPage = 0;
      pCount++;
      let quesArr = []
      let page = {
        count: pCount,
        _id: formPage._id,
        title: formPage.title,
        titleText: this.utils.getTextOutOfHtml(formPage.title),
        desc: formPage.desc,
        type: "p",
        collapseQues: (pCount == 1 ? true : false)
      }
      if(formPage.formFields){
        formPage.formFields.forEach((ques) => {
          if (ques.fieldType.family != 'statement') {
            qCount++;
            qCountInPage++;
          }
          let question = {
            count: (ques.fieldType.family != 'statement' ? qCount : ''),
            _id: ques._id,
            title: ques.title,
            titleText: this.utils.getTextOutOfHtml(ques.title),
            isHidden: ques.isHidden,
            isLogic: (ques.logicArr.length > 0 ? 1 : 0),
            logicArr: ques.logicArr,
            logicNamesArr: this.makeLogicNamesArr(ques.logicArr),
            displayCarry: 0,
            type: this.questionTypesService.getQuestionType(ques.fieldType.family, ques.fieldType.subtype),
            category: (ques.fieldType.family != 'statement' ? 'q' : 's')
          }
          quesArr.push(question);
        });
      }
      page["ques"] = quesArr;
      page["qCount"] = qCountInPage;
      this.allPagesAndQuestions['data'][formPage._id] = page;
    });
    this.setQuestionCountAndId();
  }

  makeLogicNamesArr(logicArr: any){
    let arr: any = [];
    logicArr.forEach((logicItem) => {
      if(arr.indexOf(logicItem.logic) == -1){
        arr.push(logicItem.logic);
      }
    })
    return arr;
  }

  setLogicNamesArr(pId: string, qId: string, logicArr: any){
    let quesArr: any = this.allPagesAndQuestions['data'][pId]['ques'];
    let idx: number = this.utils.findIndex(quesArr, '_id', qId);
    quesArr[idx]['logicNamesArr'] = this.makeLogicNamesArr(logicArr);
  }

  getAllPageIdsArr(){
    let arr: Array<string> = [];
    this.formObj.pageFields.forEach((page) => {
      arr.push(page._id);
    })
    return arr;
  }

  getAllQuesIdArr(){
    let arr: Array<string> = [];
    this.formObj.pageFields.forEach((page) => {
      if(page.formFields){
        page.formFields.forEach((ques) => {
          arr.push(ques._id);
        })
      }
    })
    return arr;
  }

  setQuestionCountAndId() {
    this.questionCountAndId['data'] = {};
    let qCount = 0;
    this.formObj.pageFields.forEach((page) => {
      if(page.formFields){
        page.formFields.forEach((ques) => {
          if (ques.fieldType.family != 'statement') {
            qCount++;
          }
          this.questionCountAndId['data'][ques._id] = (ques.fieldType.family != 'statement' ? qCount : '');
        })
      }
    })
  }

  getQuestionCountAndId() {
    return this.questionCountAndId;
  }

  getPageId(qId: String) {
    let pageId: string;
    this.formObj.pageFields.forEach((page) => {
      if(page.formFields){
        page.formFields.forEach((ques) => {
          if (ques._id == qId) { pageId = page._id }
        });
      }
    });
    return pageId;
  }

  getQuestionsBeforeCurrPage(pId: string) {
    let qArr: Array<any> = [];
    let qCount: number = 0;
    for (let page of this.formObj.pageFields) {
      if (page._id == pId) {
        break;
      }
      if(page.formFields){
        page.formFields.forEach((ques) => {
          if (ques.fieldType.family != 'statement') {
            qCount++;
          }
          let question = {
            count: (ques.fieldType.family != 'statement' ? qCount : ''),
            _id: ques._id,
            title: ques.title,
            type: this.questionTypesService.getQuestionType(ques.fieldType.family, ques.fieldType.subtype),
            category: (ques.fieldType.family != 'statement' ? 'q' : 's'),
            quesData: ques
          }
          qArr.push(question);
        })
      }
    }
    return qArr;
  }

  getPipingObject() {
    return this.pipingObj;
  }

  getIsPipingObject(){
    return this.isPipedObj;
  }

  setPipingObject() {
    let temp, idx;
    this.pipingObj = {};
    this.formObj.pageFields.forEach((page) => {
      if(page.formFields){
        page.formFields.forEach((ques) => {
          if (ques.piping && ques.piping.hasPiping) {
            let mongoIds = ques.title.match(/{{p:\/\/[a-f0-9]+}}/gi);
            if (mongoIds) {
              mongoIds.forEach((str) => {
                temp = str.replace('{{p://', '').replace('}}', '');
                if (this.pipingObj[ques._id]) {
                  idx = this.pipingObj[ques._id].indexOf(temp);
                  if (idx < 0) {
                    this.pipingObj[ques._id].push(temp);
                  }
                } else {
                  this.pipingObj[ques._id] = [];
                  this.pipingObj[ques._id].push(temp);
                }
              })
            }
          }
        })
      }
    })
    this.isPipedObj = {};
    for (let item in this.pipingObj) {
      for (let id of this.pipingObj[item]) {
        if (this.isPipedObj[id]) {
          idx = this.isPipedObj[id].indexOf(item);
          if (idx < 0) {
            this.isPipedObj[id].push(item);
          }
        } else {
          this.isPipedObj[id] = [];
          this.isPipedObj[id].push(item);
        }
      }
    }
  }
}



