import { Component, OnInit, Input, ViewChild, Output, EventEmitter } from '@angular/core';
import { SharedServices } from '../../../services/services.module';

@Component({
  selector: 'app-libraries',
  templateUrl: './libraries.component.html',
  styleUrls: ['../library-view.component.scss']
})
export class LibrariesComponent implements OnInit {
  @Input() libData: any;
  @Output() onFileSelect = new EventEmitter<any>();
  @Output() onFolderSelect = new EventEmitter<any>();
  @ViewChild('fileUpload') fileUpload;

  path: any = {
    '0': {
      name: '<img src="/assets/images/editSurvey/library.png">',
      compPath: '',
      type: 'allLib', //['allLib','lib','folder']
      id: null
    }
  };
  displayDetails: any = {
    displayFiles: true,
    libOrFolder: 'lib',  //['lib','folder']
  }
  allLibraries: Array<any>;
  foldersToBeDisplay: object = {};
  libContents: any; //all the contents of the selected library
  libraryFileFolder: any = {
    allData: {}, //all data within the current folder
    folderId: '', //folderId of current folder location
    grpId: '',
    grpType: '',
    libId: '',
    libSection: '',
    libType: '', //<1/2>
    fileId: '',
    fileName: '',
    selFolderId: '', //folderId of folder selected in current folder location
    selFolderName: ''
  }
  mainpath: string = ''; //complete path of the current folder location
  libType: string = ''; //<'surFold','graphFold','fileFold','messageFold','ruleFold','contactFold','dashFold','dataFold'>
  fileToUpload: any ; //file that is uploaded
  addNewItems: any = {
    display: false,
    text: ''  //['add-folder', 'add-image-file']
  }

  constructor(
    private services: SharedServices
  ) { }

  ngOnInit() {
    this.findLibType(this.libData.type);
    this.displayDetails.displayFiles = (this.libData.selType == 'folder' ? false : true);
    this.getLibraries();
  }

  getLibraries(){
    this.displayDetails.libOrFolder = 'lib';
    this.services.resource.getLibraries().subscribe((data) => {
      this.allLibraries = data;
      console.log('allLibraries', this.allLibraries);
    })
  }


  displayLibContents(libId: string, libType: number){
    this.displayDetails.libOrFolder = 'folder';
    this.mainpath = '';
    this.libraryFileFolder.allData = {};
    this.services.resource.getLibContents(libId, libType).subscribe((data) => {
      this.libContents = data;
      this.path[Object.keys(this.path).length] = {
        'name': data.name,
        'compPath': '',
        'type': 'lib',
        'id': data._id
      };
      this.libraryFileFolder.grpType = data.type;
      this.libraryFileFolder.libType = data.type;
      this.libraryFileFolder.grpId = data._id;
      this.libraryFileFolder.libId = data._id;
      console.log('libContents libraryFileFolder', this.libContents, this.libraryFileFolder);
      this.getFolders();
    })
  }


  findLibType(type: string){
    switch(type){
      case 'surveys':
        this.libType = 'surFold';
        this.libraryFileFolder.libSection = 1;
        break;
      case 'themes':
        this.libType = 'graphFold';
        this.libraryFileFolder.libSection = 2;
        break;
      case 'files':
        this.libType = 'fileFold';
        this.libraryFileFolder.libSection = 3;
        break;
      case 'msgs':
        this.libType = 'messageFold';
        this.libraryFileFolder.libSection = 4;
        break;
      case 'rules':
        this.libType = 'ruleFold';
        this.libraryFileFolder.libSection = 5;
        break;
      case 'groups':
        this.libType = 'contactFold';
        this.libraryFileFolder.libSection = 6;
        break;
      case 'dashoards':
        this.libType = 'dashFold';
        this.libraryFileFolder.libSection = 7;
        break;
      case 'datasource':
        this.libraryFileFolder.libSection = 8;
        this.libType = 'dataFold';
        break;
    }
  }

  setSelectedFolderDeatils(folderId: string, name: string){
    this.libraryFileFolder.selFolderId = folderId;
    this.libraryFileFolder.selFolderName = name;
    console.log(this.libraryFileFolder);
  }

  displayFolderContents(folderId: string, key: string){
    this.libraryFileFolder.selFolderId = '';
    this.libraryFileFolder.selFolderName = '';
    this.services.resource.getFolderContents(folderId).subscribe((data) => {
      console.log(data);
      this.mainpath = key + '@_l_@';
      this.libraryFileFolder.folderId = data.data._id;
      this.libraryFileFolder.folderName = data.data.name;
      this.path[Object.keys(this.path).length] = {
        'name': data.data.name,
        'type': 'folder',
        'compPath': key,
        'id': data.data._id
      };
      console.log('path', this.path);
      this.getFolderContents(data.data);
    })
  }

  getFolders(){
    let selFolder: string = this.mainpath;
    console.log('selFolder', selFolder);
    let nextLevelFolder: any;
    let arr: Array<string> = [];

    let reg = new RegExp("^" + selFolder + "((?!@_l_@).)*$", 'g');
    for (let key in this.libContents.library[this.libType]) {
      if (this.libContents.library[this.libType].hasOwnProperty(key)) {
        nextLevelFolder = key.match(reg);
        if (nextLevelFolder !== null) {
          arr.push(nextLevelFolder[0]);
        }
      }
    }
    arr.sort();
    let prevFoldName: Array<string> = selFolder.split('@_l_@');
    this.libraryFileFolder.folderName = prevFoldName[prevFoldName.length - 2] || '';
    console.log('prevFoldName arr',prevFoldName, arr);
    this.foldersToBeDisplay = {};
    arr.forEach((item) => {
      let obj = this.libContents.library[this.libType];
      this.foldersToBeDisplay[item] = obj[item];
    })
    console.log('foldersToBeDisplay', this.foldersToBeDisplay);
  }

  getFolderContents(data: any){
    this.libraryFileFolder.allData = {}
    data[this.libData.type].forEach((item) => {
      this.libraryFileFolder.allData[item._id] = item;
    })
    console.log('libraryFileFolder.allData', this.libraryFileFolder.allData);
    this.getFolders();
  }

  addFolder(){
    this.addNewItems.display = true;
    this.addNewItems.text = 'add-folder';
  }

  saveNewFolder(e: any){
    this.libContents.library = e.data.library;
    this.closeAddNewModal();
    this.getFolders();
    let str = this.mainpath + e.folderName.replace(' ','@_s_@').toLowerCase();
    this.libraryFileFolder.selFolderId = e.data.library[this.libType][str].id;
  }

  uploadNewFile(){
    this.closeAddNewModal();
    this.onFileSelect.emit(this.libraryFileFolder);
  }

  fileUploadChange(){
    if(this.fileUpload.nativeElement.files.length > 0){
      this.addNewItems.display = true;
      this.addNewItems.text = 'add-image-file';
      this.fileToUpload = this.fileUpload.nativeElement.files[0];
      let fileSize = this.fileToUpload.size;
      fileSize = fileSize / (1024 * 1024);
      if (fileSize > 50) {
        this.services.toastr.error("File Size cannot be greater than 20MB");
        this.fileUpload.nativeElement.value = '';
        return false;
      }
    }
  }

  selFile(id: string, name: string){
    this.libraryFileFolder.fileId = id;
    this.libraryFileFolder.fileName = name;
    console.log(this.libraryFileFolder);
    this.onFileSelect.emit(this.libraryFileFolder);
  }

  getBackPath(id: string, type: string, key: string, index: number){
    this.libraryFileFolder.selFolderId = '';
    this.libraryFileFolder.selFolderName = '';
    for(let idx in this.path){
      if(Number(idx) >= index && Number(idx) != 0){
        delete this.path[idx];
      }
    }
    if(type == 'folder'){
      console.log('key', key);
      this.displayFolderContents(id, key);
    }else if(type == 'lib'){
      this.libraryFileFolder.allData = {}; //empty files
      this.displayLibContents(this.libraryFileFolder.libId, this.libraryFileFolder.libType);
    }else if(type == 'allLib'){ //all libraries view
      this.displayDetails.libOrFolder = 'lib';
      this.libraryFileFolder.allData = {}; //empty files
      this.foldersToBeDisplay = {}; //empty folders
    }
  }

  getObjectLength(obj: any){
    return Object.keys(obj).length;
  }

  emitFolderDetails(e: any, folderId: string, name: string){
    e.stopPropagation();
    this.setSelectedFolderDeatils(folderId, name);
    this.onFolderSelect.emit(this.libraryFileFolder);
  }

  closeAddNewModal(){
    this.addNewItems.display = false;
    this.addNewItems.text = '';
  }

}



