import { Component, OnInit, Output, EventEmitter, forwardRef, Renderer2, ViewChild } from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';

export const EPANDED_TEXTAREA_VALUE_ACCESSOR : any = {
  provide: NG_VALUE_ACCESSOR,
  useExisting: forwardRef(() => TextareaExpandedComponent),
  multi: true,
};

@Component({
  selector: 'app-textarea-expanded',
  providers: [EPANDED_TEXTAREA_VALUE_ACCESSOR],
  templateUrl: './textarea-expanded.component.html',
  styleUrls: ['./textarea-expanded.component.css']
})
export class TextareaExpandedComponent implements OnInit, ControlValueAccessor {
  @ViewChild('textarea') textarea;
  @Output() onBlurEvt: EventEmitter<any> = new EventEmitter<any>();
  onChange;
  onTouched;

  writeValue( value : any ) : void {
    const div = this.textarea.nativeElement;
    this.renderer.setProperty(div, 'innerHTML', value);
  }

  registerOnChange( fn : any ) : void {
    this.onChange = fn;
  }

  registerOnTouched( fn : any ) : void {
    this.onTouched = fn;
  }

  setDisabledState( isDisabled : boolean ) : void {
    const div = this.textarea.nativeElement;
    const action = isDisabled ? 'addClass' : 'removeClass';
    this.renderer[action](div, 'disabled');
  }

  constructor(
    private renderer : Renderer2
  ) { }

  ngOnInit() {
  }

  change( $event ) {
    this.onChange($event.target.innerHTML);
    this.onTouched($event.target.innerHTML);
  }

  onBlur($event){
    this.change($event);
    this.onBlurEvt.emit();
  }

}



