import { Injectable, NgModule, ModuleWithProviders } from '@angular/core';
import { CommonModule } from '@angular/common';
import swal from 'sweetalert2';
import { CookieService } from 'ngx-cookie';
import { ToastrService } from 'ngx-toastr';

import { AppResourceService } from './resource.service';
import { ConstantsService } from './constants.service';
import { MessagesService } from './messages.service';
import { UtilsService } from './utils.service';
import { FormService } from './form.service';
import { EdataService } from './edata.service';
import { S2cFormBuilderService } from './s2c.form.builder.service';
import { TinymceService } from './tinymce.service';
import { EditConstantsService } from '../../routes/survey/edit/edit.constants.service';

@NgModule({
  imports: [],
  providers: [
    CookieService,
    AppResourceService,
    ConstantsService,
    MessagesService,
    UtilsService,
    FormService,
    EdataService,
    S2cFormBuilderService,
    TinymceService,
    EditConstantsService
  ],
  declarations: [],
  exports: []
})

export class SharedServicesModule {
  static forRoot(): ModuleWithProviders {
    return {
      ngModule: SharedServicesModule,
      providers: [
        SharedServices //added to create single instance of services in lazy loading
      ]
    };
  }
}

@Injectable()
export class SharedServices {
  resource: AppResourceService;
  constants: ConstantsService;
  messages: MessagesService;
  utils: UtilsService;
  form: FormService;
  eData: EdataService;
  alert: any;
  toastr: ToastrService;
  formBuilder: S2cFormBuilderService;
  cookie: CookieService;
  tinymce: TinymceService;

  constructor(
    private appResourceService: AppResourceService,
    private constantsService: ConstantsService,
    private messagesService: MessagesService,
    private utilsService: UtilsService,
    private formService: FormService,
    private edataService: EdataService,
    private formBuilderService: S2cFormBuilderService,
    private cookieService: CookieService,
    private toastrService: ToastrService,
    private tinymceService: TinymceService
  ) {
    this.resource = appResourceService;
    this.constants = constantsService;
    this.messages = messagesService;
    this.utils = utilsService;
    this.form = formService;
    this.eData = edataService;
    this.alert = swal;
    this.formBuilder = formBuilderService;
    this.cookie = cookieService;
    this.toastr = toastrService;
    this.tinymce = tinymceService;
  }
}



