@Pipe({
  name: 'ng2SelectPipeData',
  pure: false
})
export class Ng2SelectPipeData implements PipeTransform {

  constructor(private utils: UtilsService) {}
  transform(arr: any[]): any[] {
    let res = [];
    if(arr == null){
        return res;
    }
    arr.forEach((item, idx) => {
      res.push({
        id: item.get('_id').value,
        text: (item.get('text').value != '' ? this.utils.getTextOutOfHtml(item.get('text').value) : `Option ${idx + 1}`)
      })
    })
    return res;
  }
}

@Pipe({
  name: 'rankingHiddenRow',
  pure: false
})
export class RankingHiddenRow implements PipeTransform {

  transform(col: any[], row: any[]): any[] {
    let res: Array<any> = [];
    let isNa: number = col[col.length - 1].get('isNa').value;
    let len: number = col.length;
    let hiddenCount: number = 0;
    for(let item of row){
      if(item.get('isHidden').value){
        hiddenCount += 1;
      }
    }
    let filterLen: number = len - hiddenCount;
    isNa && filterLen--;
    for(let elem of col){
      if(col.indexOf(elem) < filterLen){
        res.push(elem);
      }
    };
    if(isNa){
      res.push(col[col.length - 1]);
    }
    return res;
  }
}

@Pipe({
  name: 'graphicalRatingNaOpt',
  pure: false
})
export class GraphicalRatingNaOpt implements PipeTransform {

  transform(arr: any[]): any[] {
    let res: Array<any> = [];
    res = arr.filter(opt => opt.controls.isNa._value == false);
    return res;
  }
}

@Pipe({
  name: 'npsLabel',
  pure: false
})
export class NpsLabel implements PipeTransform {

  transform(str: string, val: string): string {
    let idx = str.indexOf('-');
    if(idx != -1){
      if(val == 'count'){
        return str.substr(0,idx);
      }else if(val == 'text'){
        return str.substr(idx + 1);
      }
    }
    return str;
  }
}

export const QuesTemplatePipe = [Ng2SelectPipeData, RankingHiddenRow, GraphicalRatingNaOpt, NpsLabel];



