import { Component, OnInit, Input, Output, EventEmitter, ViewChild } from '@angular/core';
import { SharedServices } from '../../../../services/services.module';

@Component({
  selector: 'app-add-new-folder',
  templateUrl: './add.new.folder.component.html',
  styleUrls: ['../../library-view.component.scss']
})
export class AddNewFolderComponent implements OnInit {
  @ViewChild('addFolderInp') addFolderInp;
  @Input() libFolderDetails: any;
  @Output() closeModal = new EventEmitter<any>();
  @Output() addFolder = new EventEmitter<any>();
  newFolderDetails: any = {
    name: 'Untitled Folder'
  }

  constructor(
    private services: SharedServices
  ) { }

  ngOnInit() {
    setTimeout(() => {
      this.addFolderInp.nativeElement.select();
    },1);
  }

  onKeyUpInSavingFolder(e: any){
    if(e.which == 13){
      this.saveNewFolder();
    }
  }

  saveNewFolder(){
    if(!this.services.utils.isFolderNameValid(this.newFolderDetails.name) || this.newFolderDetails.name.trim() == ''){
      this.services.toastr.error('Please enter the Folder Name.', 'Cannot be Saved.');
    }else{
      let libDetails: any = this.libFolderDetails.libraryFileFolder;
      console.log('libraryFileFolder', this.libFolderDetails.libraryFileFolder);
      console.log('mainpath', this.libFolderDetails.mainpath);
      let result: string = this.libFolderDetails.mainpath.substring(0, this.libFolderDetails.mainpath.lastIndexOf("@_l_@"));
      this.services.resource.saveNewFolderInLibrary(libDetails.libId, result, libDetails.libSection, libDetails.libType, this.newFolderDetails).subscribe((data) => {
        this.addFolder.emit({'data': data, 'folderName': this.newFolderDetails.name});
        this.newFolderDetails.name = 'Untitled Folder';
      },
      (err) => {
        this.services.toastr.error('Folder Name with same name exists','Cannot be added');
      })
    }
  }

  cancelAddFolder(){
    this.newFolderDetails.name = 'Untitled Folder';
    this.closeModal.emit();
  }

}



