import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'app-slider-type3',
  templateUrl: './slider-type3.component.html',
  styleUrls: ['./slider-type3.component.css', '../slider.component.scss']
})
export class SliderType3Component implements OnInit {
  @Input() sliderData: any;
  @Input() rating: any;
  @Output() onChangeSubSlider = new EventEmitter<any>();
  completeQuestion: any;
  constructor() { }

  ngOnInit() {
    // console.log(this.sliderData)
    // this.completeQuestion = this.sliderData;
    // this.reBuildData();
  }

  onSliderChange(event){
    this.onChangeSubSlider.emit({"rating":this.rating});
  }
}



