import { Injectable } from '@angular/core';

@Injectable()
export class EdataService {
  eDataObject = {
    contact: null,
    qParams: null,
    responseBodyEdata: null,
    collectorType: -1
  };

  constructor(

  ) {}

  setIntialData(surveyData: object, qParams: object) {
    if(surveyData['surveyBody'])
      this.eDataObject.contact = this.manipulateContactEdata(surveyData['contact'], surveyData['surveyBody']._id);
    if(surveyData['responseBody'])
      this.eDataObject.responseBodyEdata = surveyData['responseBody'] && surveyData['responseBody'].eData;
    if(surveyData['collector'])
      this.eDataObject.collectorType = this.eDataObject.collectorType == -1 ? surveyData['collector'].type : this.eDataObject.collectorType;
      
    this.eDataObject.qParams = qParams;
  }

  getEdataString(type: string, value: string) {
    let result = "";
    if(type && value) {
      value = encodeURIComponent(value);
      result = '{{' + type + '://' + value + '}}';
    }
    return result;
  }

  getDataFromString(expression: string) {
    let result = {type: "", value: ""};
    try {
      expression = expression.replace('{{', '').replace('}}', '');
      result.type = expression.split('://')[0];
      result.value = decodeURIComponent(expression.split('://')[1]);
    } catch (e) {}
    return result;
  }

  evaluateEdata(expression: string) {
    let result = '';
    try {
      let details = this.getDataFromString(expression);
      let value = details.value;
      if (details.type == 'm') {
        result = value;
      } else if(this.eDataObject.responseBodyEdata && this.eDataObject.responseBodyEdata[value]) {
        result = this.eDataObject.responseBodyEdata[value];
      } else if (this.eDataObject.collectorType != -1) {
        if ([0, 2, 4].indexOf(this.eDataObject.collectorType) != -1) {
          result = this.eDataObject.qParams[value] || '';
        } else if ([1, 5].indexOf(this.eDataObject.collectorType) != -1) {
          if (details.type == 'e') {
            if (this.eDataObject.contact && this.eDataObject.contact.eData && this.eDataObject.contact.eData[value]) {
              result = this.eDataObject.contact.eData[value];
            }
          } else if (details.type == 'c') {
            if (this.eDataObject.contact && this.eDataObject.contact[value]) {
              result = this.eDataObject.contact[value];
            }
          }
        }
      }
    }catch (ex) {console.error(ex)}

    return result;
  }

  manipulateContactEdata(contact: object, surId: string) {
    let eData = {};
    contact && contact['eData'] && contact['eData'].forEach((item) => {
      item.eDataKeyArr.forEach((eDataKey) => {
        let value = item.eData[eDataKey][0].value;
        eDataKey = decodeURIComponent(eDataKey);
        if (item.survey && item.survey._id == surId) {
          eData[eDataKey] = value;
        } else if (item.survey == null) {
          !eData.hasOwnProperty(eDataKey) && (eData[eDataKey] = value);
        }
      });
    });
    !contact && (contact = {});
    contact['eData'] = eData;
    return contact;
  }

}



