import { LayoutComponent } from '../layout/layout.component';

export const routes = [
  {
    path: 'survey',
    component: LayoutComponent,
    children: [
      /*{ path: '', redirectTo: 'survey', pathMatch: 'full' },*/
      { path: '', loadChildren: './survey/survey.module#SurveyModule' }
    ]
  },

  {
    path: '',
    children: [
      /*{ path: '', redirectTo: 'survey', pathMatch: 'full' },*/
      { path: '', loadChildren: './take-survey/take.survey.module#TakeSurveyModule' }
    ]
  },

  // Not found
  // { path: '**', redirectTo: 'survey' }

];



