import { Component, OnInit, OnChanges, SimpleChanges, Input, Output, EventEmitter } from '@angular/core';
import { ConstantsService } from '../../services/constants.service';

@Component({
  selector: 'app-file-upload',
  templateUrl: './file-upload.component.html',
  styleUrls: ['../question-templates.scss', './file-upload.component.scss']
})
export class FileUploadComponent implements OnInit, OnChanges {
  @Input() questionData: any;
  @Input() changesInChild: any;
  @Input() responseData: any;
  @Input() langDirection: string;
  @Output() onChange: EventEmitter<any> = new EventEmitter<any>();
  completeQuestion: any;
  ansData: any;
  acceptType: string;
  ans: any;
  maxSize: string;
  fileType: string;
  showMaxSizeError: boolean = false;
  showInvalidFileError: boolean = false;
  imageSrc: string;

  constructor(
    private constServ: ConstantsService
  ) { }

  ngOnInit() {
    this.completeQuestion = this.questionData;
    this.setQuestionToBeDisplayed();
    this.responseData && this.setResponse();
  }

  ngOnChanges(changes: SimpleChanges){
    if(this.completeQuestion){
      this.setQuestionToBeDisplayed();
    }
  }

  setQuestionToBeDisplayed(){
    let acceptStr: string = this.completeQuestion.data.get('fieldOptions.upload').at(0).get('fieldVald.format').value;

    if(acceptStr)
      this.acceptType = acceptStr.split(',').map(item => '.' + item.trim()).join(',');

    let maxFileSize = this.completeQuestion.data.get('fieldOptions.upload').at(0).get('fieldVald.maxFileSize').value//comes in byte
    let fileType = this.completeQuestion.data.get('fieldOptions.upload').at(0).get('fieldVald.format').value//comes in comma separated
    if(maxFileSize)
      this.maxSize = maxFileSize;
    if(fileType)
      this.fileType = fileType;
  }

  setResponse() {
    this.imageSrc = this.constServ.apiUrl + "v1/response/image/" + this.completeQuestion.data.get('surId').value + "/" + this.responseData[0].respId + "/" + this.completeQuestion.data.get('_id').value+ "/" +this.responseData[0].uploadId + "?__t="+(new Date).getTime();
  }

  updateResponse(event) {
    let that = this;
    let uploadId = this.completeQuestion.data.get('fieldOptions.upload').at(0).get('_id').value;

    let fileList: FileList = event.target.files;
    let file: File = fileList[0];

    if(file && this.fileType && (this.fileType.split(",").indexOf(file.name.split('.').pop()) == -1)){
      this.showInvalidFileError = true;
      return;
    }else{
      this.showInvalidFileError = false;
    }

    if(file && this.maxSize && file.size > (parseFloat(this.maxSize)*1024*1024)){
      this.showMaxSizeError = true;
      return;
    }else{
      this.showMaxSizeError = false;
    }

    if (file) {
      let that = this;
      let reader: FileReader = new FileReader();
      reader.onload = function (e) {
        that.imageSrc = e.target['result'];
      };
      reader.readAsDataURL(file);
    } else {
      that.imageSrc = '';
    }
    let fileData = {id: uploadId, file: file}
    file && (this.ansData = [{text: file.name, uploadId: uploadId}]);

    let qId = this.completeQuestion.data.get('_id').value;
    console.log(qId, event);
    this.onChange.emit({
      qId: qId,
      ansData: this.ansData,
      fileData: fileData,
      validPresent: false,
      validated: false,
      addtionalTextPresent: false,
      additionaTextValidationPresent: false,
      additionaTextValidated: false,
      completedResp: (this.ansData.length == 1)
    });
  }

}



