import { Injectable } from '@angular/core';

@Injectable()
export class ConstantsService {
	apiUrl: string = 'http://localhost:9995/';

  options: any = {
    eData: {text: "Embedded Data", value: "e"},
    contacts: {text: "Contacts", value: "c"},
    manual: {text: "Manual", value: "m"},
    questions: {text: "Questions", value: "p"}
  };

  typeOfEmbeddedData: Array<any> = [
    this.options.eData, this.options.contacts, this.options.manual
  ];

  typeOfPipingDataQues: Array<any> = [
    this.options.eData, this.options.contacts, this.options.questions
  ];

	typeOfPipingDataOpt: Array<any> = [
		this.options.eData, this.options.contacts
	]

  contactFields: Array<any> = [
    {text: "First Name", value: "fName"},
    {text: "Last Name", value: "lName"},
    {text: "Email", value: "email"},
    {text: "Mobile", value: "mobile"},
    {text: "Unique ID", value: "extUniqueId"}
  ]

	tagTypes: Array<any> = [
		{text: "Contacts", value: "contactsData"},
		{text: "Question", value: "questions"},
		{text: "Embedded Data", value: "embedData"},
		{text: "Manual", value: "manualData"}
	]
	fontFamList: any = [
	  {text: 'Helvetica Neue', value: 'Helvetica Neue'},
	  {text: 'Times New Roman', value: 'Times New Roman'},
	]
  constructor(

  ) {}

}



