import { Component, OnInit, OnChanges, Output, EventEmitter, Input, SimpleChanges } from '@angular/core';
import { SharedServices } from '../../../shared/services/services.module';

@Component({
  selector: 'app-slider',
  templateUrl: './slider.component.html',
  styleUrls: ['./slider.component.scss']
})
export class SliderComponent implements OnInit, OnChanges {
  @Input() questionData;
  @Input() responseData;
  @Input() langDirection: string;
  @Output() onChange: EventEmitter<any> = new EventEmitter<any>();
  @Input() changesInChild;
  completeQuestion: any;
  graphicalSliderConfig: any = {
    connect: [true,false],
    step: 1,
    range: {
      min: 1,
      max: 10
    }
  };
  naSelected: boolean = false;
  rating: any = 2;
  discreteArray: any = [];
  diffDiscreteValue: number;
  showSlider: boolean = true;
  ansData:any = [];
  newRating:any = 0;
  tempRating:any = 0;
  ratingFlag:number = 0;
  sliderData: any;

  constructor(
    private services: SharedServices
  ) { }

  ngOnInit() {
    this.completeQuestion = this.questionData;
    this.reBuildData();
  }

  ngOnChanges(changes: SimpleChanges){
    if(this.completeQuestion){
      this.reBuildData();
    }
  }

  changeNewRating(rat){
    this.rating = rat;
    this.ratingFlag=0;
    this.newRating=this.rating;
  }

  updateResponse(type = null) {
    this.ansData = [];
    let rowId = this.completeQuestion.data.get('fieldOptions.row').at(0).get('_id').value;
    console.log("rowId",rowId)
    let item: any = null;

    if(type != "NA")
      this.naSelected = false;
    else
      this.naSelected = true;

    if(type != "NonNA"){
      let tempRating = this.rating;
      if(type == "NA")
        tempRating = "NA"
      item= {text: tempRating, rowId: rowId};
      this.ansData.push(item);
    }

    let qId = this.completeQuestion.data.get('_id').value;
    this.onChange.emit({
      qId: qId,
      ansData: this.ansData,
      validPresent: false,
      validated: false,
      addtionalTextPresent: false,
      additionaTextValidationPresent: false,
      additionaTextValidated: false,
      completedResp: this.ansData.length >= 1
    });
  }

  enterRating(e: any){
    // if(e.key=="Enter"){
    this.validateRating();
    // }
  }

  addNaOption(event){
    if(event.target.checked){
      console.log(event)
      this.rating = this.newRating = 0;
      this.updateResponse('NA');
    }else{
      this.rating = this.newRating = 0;
      this.updateResponse('NonNA')
    }
  }

  validateRating(){
    this.tempRating = this.rating;
      let changeRating = this.newRating;
      if((changeRating > parseInt(this.graphicalSliderConfig.range.max))||(changeRating < parseInt(this.graphicalSliderConfig.range.min))){
        this.ratingFlag = 1;
        this.services.toastr.error('Please enter the value in range');
        return;
      }
      else{
        if(this.newRating%parseInt(this.graphicalSliderConfig.step)!=0){
          this.services.toastr.error('Please enter the correct divisible value');
          this.ratingFlag = 1;
          return;
        }
        this.ratingFlag = 0;
        this.rating=Math.ceil((changeRating)/parseInt(this.graphicalSliderConfig.step))*parseInt(this.graphicalSliderConfig.step);
      }
      this.updateResponse();
  }


  reBuildData(){
    this.ratingFlag=0;
    this.graphicalSliderConfig.step = parseInt(this.completeQuestion.data.get('properties.step').value);
    this.graphicalSliderConfig.range.min = parseInt(this.completeQuestion.data.get('properties.minValue').value);
    this.graphicalSliderConfig.range.max = parseInt(this.completeQuestion.data.get('properties.maxValue').value);
    this.newRating = this.rating = parseInt(this.completeQuestion.data.get('properties.start').value);
    if(typeof this.responseData != "undefined" && this.responseData[0]){
      if(this.responseData[0].text == "NA"){
        this.newRating = this.rating = 0;
        this.naSelected = true;
      }
      else{
        this.newRating = this.rating = this.responseData[0].text;
        this.naSelected = true;
      }
    }

    if(this.completeQuestion.data.get('properties.valType').value == 'discrete'){
      this.discreteArray = [];
      let num: number = this.graphicalSliderConfig.range.min;
      while(num <= this.graphicalSliderConfig.range.max){
        this.discreteArray.push(num);
        num += this.graphicalSliderConfig.step;
      }
    }

    this.sliderData = {
      type: this.completeQuestion.data.get('properties.valType').value, //['discrete','continous']
      config: this.graphicalSliderConfig,
      allowLabels: true,
      labels: {
        minLabel: this.completeQuestion.data.get('properties.minLabel'),
        maxLabel: this.completeQuestion.data.get('properties.maxLabel')
      },
      discreteArray: this.discreteArray,
      allowPips: true,
      tooltip: true
    }
    this.showSlider = false;
    setTimeout(() => {
      this.showSlider = true;
    },1);
  }

  onChangeSlider(event){
    this.changeNewRating(event.rating);
    this.updateResponse();
  }
}



