import { Injectable } from '@angular/core';
import { FormArray, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { Subject } from 'rxjs';

import { UtilsService } from './utils.service';

@Injectable()
export class S2cFormBuilderService {

  constructor(
  	private _fb: FormBuilder,
    private utils: UtilsService
  ) {}

  buildForm(data: object, item?: object): FormGroup {
  	let result = this.setFormData(this.utils.cloneObject(data), item);
  	return result;
  }

  getFormGroup(data: any) {
    return this._fb.group(data);
  }

  getFormArray() {
    return this._fb.array([]);
  }

  setFormData(data: any, item?: object): FormGroup {
    // console.log(data, item);
    for(let key in data) {
      if (Array.isArray(data[key])) {
        let formArray = this.getFormArray();
        //data[key][0] && formArray.push(this.setFormData(data[key][0], item?item[key]: undefined));
        data[key].forEach(keyData => {
          if (Object.prototype.toString.call(keyData) == "[object Object]") {
            formArray.push(this.setFormData(keyData, item?item[key]: undefined));
          } else {
            formArray.push(new FormControl(keyData));
          }
        });
        data[key] = formArray;
      } else if (Object.prototype.toString.call(data[key]) == "[object Object]") {
        data[key] = this.setFormData(data[key], item?item[key]: undefined);
      }
    }

    return this.getFormGroup(data);
  }

}



