import { Component, OnInit, OnChanges, SimpleChanges, Input, Output, EventEmitter, HostListener } from '@angular/core';
import { UtilsService } from '../../services/utils.service';
import { QuestionTypesService } from '../question-types.service';

@Component({
  selector: 'app-net-promoter-score',
  templateUrl: './net-promoter-score.component.html',
  styleUrls: ['../question-templates.scss', './net-promoter-score.component.scss']
})
export class NetPromoterScoreComponent implements OnInit, OnChanges {
  @Input() questionData: any;
  @Input() responseData?: any;
  @Input() langDirection: string;
  @Input() optTheme: any;
  @Output() onChange: EventEmitter<any> = new EventEmitter<any>();
  completeQuestion: any;
  ansData: Array<any> = [];
  answer: number = -1;
  currInnerWidth: number;
  prevInnerWidth: number;
  fixedNpsColorArr: string[] = ['#df3f2c','#e13f2c','#e44139','#e65454','#e96d6e','#eb8281','#ef9e9e','#fab847','#f8a630','#a1c320','#809b16'];
  fixedNpsFontColorArr: string[] = ['#fff','#fff','#fff','#fff','#fff','#fff','#fff','#fff','#fff','#fff','#fff'];
  fixedNpsNoColorArr: string[] = ['#f1f1f1','#f1f1f1','#f1f1f1','#f1f1f1','#f1f1f1','#f1f1f1','#f1f1f1','#f1f1f1','#f1f1f1','#f1f1f1','#f1f1f1'];
  fixedNpsNoFontColorArr: string[] = ['#000','#000','#000','#000','#000','#000','#000','#000','#000','#000','#000'];
  npsColorArr: string[] = [];
  npsFontColorArr: string[] = [];
  npsNoColorArr: string[] = [];
  npsNoFontColorArr: string[] = [];
  npsColorEmoji: string[] = ['color-emoji1','color-emoji2','color-emoji3','color-emoji4','color-emoji5','color-emoji6','color-emoji7','color-emoji8','color-emoji9','color-emoji10','color-emoji11'];
  npsNoColorEmoji: string[] = ['smiley-unhappy','smiley-unhappy','smiley-unhappy','smiley-unhappy','smiley-unhappy','smiley-unhappy','smiley-unhappy','smiley-neutral','smiley-neutral','smile-happy','smile-happy'];
  emojiInSilder: string[] = ['sad-emoji0', 'sad-emoji1', 'sad-emoji2', 'sad-emoji3', 'sad-emoji4', 'sad-emoji5', 'sad-emoji6', 'neutral-emojis7', 'neutral-emojis8', 'happy-emoji9', 'happy-emoji10'];
  npsSliderConfig: any = {
    step: 1,
    range: {
      min: 0,
      max: 10
    }
  }
  npsProp:any = {
    type: '',
    color :''
  }


  constructor(
    private utils: UtilsService,
    private qTypesServ: QuestionTypesService
  ) { }

  @HostListener('window:resize', ['$event'])
  onResize(event) {
    this.checkTypeAndColor();
  }

  ngOnInit() {
    this.completeQuestion = this.questionData;
    this.checkTypeAndColor();
    this.responseData && this.setResponse();
  }

  checkTypeAndColor(){
    this.currInnerWidth = window.innerWidth;
    if(this.completeQuestion.data.get('properties') && this.completeQuestion.data.get('properties.nps.differentView').value){
      if(this.currInnerWidth < 480 && (this.prevInnerWidth >= 480 || this.prevInnerWidth == undefined)){
        if(this.completeQuestion.data.get('properties')){
          this.npsProp.type = this.completeQuestion.data.get('properties.nps.mobile.type');
          this.npsProp.color = this.completeQuestion.data.get('properties.nps.mobile.mColor');
        }
      }else if(this.currInnerWidth > 480 && (this.prevInnerWidth <= 480 || this.prevInnerWidth == undefined)){
        if(this.completeQuestion.data.get('properties')){
          this.npsProp.type = this.completeQuestion.data.get('properties.nps.desktop.type');
          this.npsProp.color = this.completeQuestion.data.get('properties.nps.desktop.dColor');
        }
      }
    }else{
      if(this.completeQuestion.data.get('properties')){
        this.npsProp.type = this.completeQuestion.data.get('properties.nps.desktop.type');
        this.npsProp.color = this.completeQuestion.data.get('properties.nps.desktop.dColor');
      }else{
        this.npsProp.type = {value: 'tiles-h'};
        this.npsProp.color = {value: true};
      }
    }
    this.prevInnerWidth = window.innerWidth;
  }

  ngOnChanges(changes: SimpleChanges){
    this.npsColorArr = this.utils.cloneObject(this.fixedNpsColorArr);
    this.npsNoColorArr = this.utils.cloneObject(this.fixedNpsNoColorArr);
    this.npsFontColorArr = this.utils.cloneObject(this.fixedNpsFontColorArr);
    this.npsNoFontColorArr = this.utils.cloneObject(this.fixedNpsNoFontColorArr);
    if(changes.optTheme && !changes.optTheme.currentValue){
      return;
    }
    let npsColorObj: any = changes.optTheme.currentValue.nps;
    if(npsColorObj.detractor && npsColorObj.detractor.color) {
      this.npsColorArr.fill(npsColorObj.detractor.color,0,7);
      this.npsFontColorArr.fill(npsColorObj.detractor.fontColor,0,7);
    }
    if(npsColorObj.passive && npsColorObj.passive.color) {
      this.npsColorArr.fill(npsColorObj.passive.color,7,9);
      this.npsFontColorArr.fill(npsColorObj.passive.fontColor,7,9);
    }
    if(npsColorObj.promoter && npsColorObj.promoter.color) {
      this.npsColorArr.fill(npsColorObj.promoter.color,9,11);
      this.npsFontColorArr.fill(npsColorObj.promoter.fontColor,9,11);
    }
    if(npsColorObj.noColor){
      for(let noColIdx = 0; noColIdx < 11; noColIdx++){
        if(npsColorObj.noColor[noColIdx]) {
          (npsColorObj.noColor[noColIdx]['color']) && (this.npsNoColorArr[noColIdx] = npsColorObj.noColor[noColIdx]['color']);
          (npsColorObj.noColor[noColIdx]['fontColor']) && (this.npsNoFontColorArr[noColIdx] = npsColorObj.noColor[noColIdx]['fontColor']);
        }
      }
    }
  }

  setResponse() {
    this.ansData = this.responseData;
    this.responseData.forEach(item=>{
      let colIdx = this.qTypesServ.getIndexById(this.completeQuestion.data.get('fieldOptions.col').value, item.colId);
      this.answer = colIdx;
    });
  }

  onSelectionChange(idx = null) {
    let qId = this.completeQuestion.data.get('_id').value;

    if(idx){
      if(this.answer && idx == this.answer){
        this.answer = null;
        this.ansData = [];
      }else{
        this.answer = idx;
        let currentCol = this.completeQuestion.data.get('fieldOptions.col').value[this.answer];
        let colId = currentCol && currentCol._id;
        this.ansData = [{colId: colId, rowId: this.completeQuestion.data.get('fieldOptions.row').value[0]._id}];
      }
    }

    this.onChange.emit({
      qId: qId,
      ansData: this.ansData,
      validPresent: false,
      validated: false,
      addtionalTextPresent: false,
      additionaTextValidationPresent: false,
      additionaTextValidated: false,
      completedResp: (this.ansData.length == 1)
    });
  }

  onChangeNpsArc(e: any){
    this.onSelectionChange(e);
  }

}



