import { Injectable } from '@angular/core';

import { QuestionTypesService } from '../questionTemplates/question-types.service';
import { UtilsService } from './utils.service';
import { AppResourceService } from './resource.service';
// import { EditConstantsService } from '../../routes/survey/edit/edit.constants.service';

declare var FormResponse;

@Injectable()
export class SurveyValidationService {

  checkValidation(row, col, ans){
    console.log("rowArr",row.validation);
    console.log("colArr",col.validation);
    console.log("ansData",ans);
    let ret = false;
    if(row.validation && row.validation.type && (row.validation.type != "")){
      switch(row.validation.type){
        case "text":
          ret = this.isText(row.validation.subType, row.validation.min.trim(), row.validation.max.trim(), ans);
          break;
        case "number":
          ret = this.isNumber(row.validation.subType, row.validation.min.trim(), row.validation.max.trim(), ans);
          break;
        case "date":
          ret = this.isDate(row.validation.subType, ans);
          break;
        default:
          break;
        }
        return {"ret":ret, "msg": (row.validation.text)?row.validation.text:"Validation Error"};
    }

    if(col.validation && col.validation.type && (col.validation.type != "")){
      switch(col.validation.type){
        case "text":
          ret = this.isText(col.validation.subType, col.validation.min.trim(), col.validation.max.trim(), ans);
          break;
        case "number":
          ret = this.isNumber(col.validation.subType, col.validation.min.trim(), col.validation.max.trim(), ans);
          break;
        case "date":
          ret = this.isDate(col.validation.subType, ans);
          break;
        default:
          break;
        }
        return {"ret":ret, "msg": (col.validation.text)?col.validation.text:"Validation Error"};
    }

    return {"ret":true, "msg": null};
  }

  checkAdditionalTextValidation(row, col, ans){
    console.log("AddrowArr",row.addnTextvalidation);
    console.log("AddcolArr",col.addnTextvalidation);
    console.log("AddansData",ans);
    let ret = false;
    if(row.addnTextvalidation && row.addnTextvalidation.type && (row.addnTextvalidation.type != "")){
      switch(row.addnTextvalidation.type){
        case "text":
          ret = this.isText(row.addnTextvalidation.subType, row.addnTextvalidation.min.trim(), row.addnTextvalidation.max.trim(), ans);
          break;
        case "number":
          ret = this.isNumber(row.addnTextvalidation.subType, row.addnTextvalidation.min.trim(), row.addnTextvalidation.max.trim(), ans);
          break;
        case "date":
          ret = this.isDate(row.addnTextvalidation.subType, ans);
          break;
        default:
          break;
        }
        return {"ret":ret, "msg": row.addnTextvalidation.text};
    }

    if(col.addnTextvalidation && col.addnTextvalidation.type && (col.addnTextvalidation.type != "")){
      switch(col.addnTextvalidation.type){
        case "text":
          ret = this.isText(col.addnTextvalidation.subType, col.addnTextvalidation.min.trim(), col.addnTextvalidation.max.trim(), ans);
          break;
        case "number":
          ret = this.isNumber(col.addnTextvalidation.subType, col.addnTextvalidation.min.trim(), col.addnTextvalidation.max.trim(), ans);
          break;
        case "date":
          ret = this.isDate(col.addnTextvalidation.subType, ans);
          break;
        default:
          break;
        }
        return {"ret":ret, "msg": col.addnTextvalidation.text};
    }

    return {"ret":true, "msg": null};
  }

  isNumber(type, min, max, text){
    let ret = false;
    switch(type){
      case "Integer":
        ret = (/^[-+]?[0-9]+$/.test(text)) && !isNaN(text);
        break;
      case "wholeNumber":
        ret = (/^[0-9]+$/.test(text)) && !isNaN(text);
        break;
      case "naturalNumber":
        ret = (/^[1-9][0-9]*$/.test(text)) && !isNaN(text);
        break;
      case "decimalNumber":
        ret = (/^[0-9]+(\.[0-9]+)?$/.test(text)) && !isNaN(text);
        break;
      default:
        break;

    }
// console.log("retPrev", ret)
    if(min != "" && !isNaN(min))
      ret = ret && (parseInt(text) >= parseInt(min));
    if(max != "" && !isNaN(max))
      ret = ret && (parseInt(text) <= parseInt(max));
      //
      // console.log("ret", ret)
      // console.log("text.length", text.length)
      // console.log("rparseInt(min)et", (parseInt(text) >= parseInt(min)))
      // console.log("parseInt(min)", (parseInt(text) <= parseInt(max)))

    return ret;
  }

  isText(type, min, max, text){
    let ret = false;
    switch(type){
      case "normalText":
        ret = true;
        break;
      case "email":
        let regEm = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@(([[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{1,}))$/;
        ret = regEm.test(text.toLowerCase());
        break;
      default:
        break;

    }

    if(min != "" && !isNaN(min))
      ret = ret && (text.length >= parseInt(min));

    if(max != "" && !isNaN(max))
      ret = ret && (text.length <= parseInt(max));
// console.log("text.length", text.length)
// console.log("rparseInt(min)et", (text.length >= parseInt(min)))
// console.log("parseInt(min)", (text.length <= parseInt(max)))
    return ret;
  }

  isDate(type, text){
    let ret = false;
    switch(type){
      case "withoutTime":
        ret = this.checkDMY(text, false);
        break;
      case "withTime":
        ret = this.checkDMY(text, true);
        break;
      default:
        break;

    }
    return ret;
  }

  checkDMY(value, time){
    let ddmmyyyy = false;
    let mmddyyyy = false;
    let DMYArr = [];
    let ret = false;

    if(time){
      ddmmyyyy = /^(0?[1-9]{1}|[1-2][0-9]|30|31)(\/|-)(0?[1-9]{1}|10|11|12)(\/|-)([0-9]{1,4})(\s+)(0?[0-9]|1[0-9]|2[0-3])(:)(0?[0-9]|[0-5][0-9])((:)(0?[0-9]|[0-5][0-9]))?$/.test(value);
      mmddyyyy = /^(0?[1-9]{1}|10|11|12)(\/|-)(0?[1-9]{1}|[1-2][0-9]|30|31)(\/|-)([0-9]{1,4})(\s+)(0?[0-9]|1[0-9]|2[0-3])(:)(0?[0-9]|[0-5][0-9])((:)(0?[0-9]|[0-5][0-9]))?$/.test(value);
      let DTArr = value.split(/\s+/);
      DMYArr = DTArr[0].split(/-|\//);
    }else{
      ddmmyyyy = /^(0?[1-9]{1}|[1-2][0-9]|30|31)(\/|-)(0?[1-9]{1}|10|11|12)(\/|-)([0-9]{1,4})$/.test(value);
      mmddyyyy = /^(0?[1-9]{1}|10|11|12)(\/|-)(0?[1-9]{1}|[1-2][0-9]|30|31)(\/|-)([0-9]{1,4})$/.test(value);
      DMYArr = value.split(/-|\//);
    }
    // console.log("DMYArr", DMYArr)
    // console.log("mmddyyyy", mmddyyyy)
    // console.log("ddmmyyyy",ddmmyyyy)

    if(!mmddyyyy && !ddmmyyyy)
      return ret;

    let dd = (ddmmyyyy)? parseInt(DMYArr[0]): parseInt(DMYArr[1]);
    let mm = (ddmmyyyy)? parseInt(DMYArr[1]): parseInt(DMYArr[0]);
    let yy = parseInt(DMYArr[2]);
    let ListofDays = [31, 28, 31, 30, 31, 30, 31, 31, 30, 31, 30, 31];
    if (mm == 1 || (mm > 2 && mm < 13)) {
        if (dd <= ListofDays[mm - 1] || dd > 0) {
          ret = true;
        }
    }
    if (mm == 2) {
        let lyear = false;
        if ((!(yy % 4) && yy % 100) || !(yy % 400)) {
            lyear = true;
        }
        if ((lyear == false) && (dd < 29 && dd > 0)) {
            ret = true;
        } else if ((lyear == true) && (dd <= 29 && dd > 0)) {
            ret = true;
        }
    }
    return ret;
  }
}



