import { Component, OnInit, OnChanges, SimpleChanges, Input, Output, EventEmitter, ChangeDetectorRef, OnDestroy } from '@angular/core';
import { SharedServices } from '../services/services.module';

@Component({
  selector: 'app-question-template',
  templateUrl: './question.template.component.html',
  styleUrls: ['./question-templates.scss']
})
export class QuestionTemplateComponent implements OnInit, OnChanges, OnDestroy {
  @Input() questionData: any;
  @Input() responseData: any;
  @Input() rateVisible?: boolean;
  @Input() themeInp: any;
  @Input() langDirection: string;
  @Output() onChange: EventEmitter<any> = new EventEmitter<any>();
  completeQuestion: any;
  changesToChildComp: any;
  selQuesType: any = {};
  qCount: number;
  formObj: any;
  intervalRef: any;

  constructor(
    private ref: ChangeDetectorRef,
    private services: SharedServices
  ) {
    // ref.detach();
    // this.intervalRef = setInterval(() => {this.ref.detectChanges()}, 500);
  }

  ngOnInit() {
    !this.rateVisible && (this.rateVisible = true);
    this.setData();
    this.changesToChildComp = this.questionData.changesInChild;
    this.formObj = this.services.form.formObj;
  }

  ngOnChanges(changes: SimpleChanges){
    if(changes.questionData){
      let currChanges = changes.questionData.currentValue.changesInChild;
      if(currChanges && currChanges.from == 'main-list'){
        this.setData();
      }else{
        this.changesToChildComp = currChanges;
      }
    }
  }

  setData(){
    this.selQuesType.type = null;
    this.completeQuestion = this.questionData.form.data;
    this.qCount = this.questionData.count;
    setTimeout(() => {
      this.selQuesType = this.services.utils.cloneObject(this.questionData.form.type);
    },0);
  }

  getText(str: string){
    return this.services.utils.getTextOutOfHtml(str);
  }

  ngOnDestroy() {
    clearInterval(this.intervalRef);
  }

}



