import { Component, OnInit, Input, Output, EventEmitter, ViewChild } from '@angular/core';
import { SharedServices } from '../../../../services/services.module';

@Component({
  selector: 'app-add-new-image',
  templateUrl: './add.new.image.component.html',
  styleUrls: ['../../library-view.component.scss']
})
export class AddNewImageComponent implements OnInit {
  @Input() uploadImgData: any;
  @Output() closeModal = new EventEmitter<any>();
  @Output() addFile = new EventEmitter<any>();

  @ViewChild('addItemInp') addItemInp;

  displayUploadProgress: boolean = false;
  fileUploadDetails: any = {
    desc: '',
    name: '',
    size: '',
    type: ''
  }

  constructor(
    private services: SharedServices
  ) { }

  ngOnInit() {
    setTimeout(() => {
      this.addItemInp.nativeElement.select();
    },1);
    let file = this.uploadImgData.fileToUpload;
    this.fileUploadDetails.desc = file.name.split('.')[0];
    this.fileUploadDetails.name = file.name;
    let size: number = file.size / (1024 * 1024);
    this.fileUploadDetails.size = (Math.round(size * 100) / 100) + 'MB';
    this.fileUploadDetails.type = file.type;
  }

  onKeyUpUploadItem(e: any){
    if(e.which == 13){
      this.saveNewFile();
    }
  }

  saveNewFile(){
    let libfilefolderDetails: any = this.uploadImgData.libraryFileFolder;
    if (libfilefolderDetails.hasOwnProperty('grpId') && libfilefolderDetails.grpId != null) {
      if (libfilefolderDetails.hasOwnProperty('folderId') && libfilefolderDetails.folderId != null) {
        let folderData: any = {};
        folderData.libId = libfilefolderDetails.grpId;
        folderData.libType = libfilefolderDetails.grpType;
        folderData.folderId = libfilefolderDetails.folderId;
        folderData.fileDesc = this.fileUploadDetails.desc;
        let toSendPutFormdata = new FormData();
        toSendPutFormdata.append('data', JSON.stringify(folderData));
        toSendPutFormdata.append('file', this.uploadImgData.fileToUpload);
        this.displayUploadProgress = true;
        this.services.resource.saveImgFileInLibrary(toSendPutFormdata).subscribe((data) => {
          libfilefolderDetails.fileId = data.data._id;
          libfilefolderDetails.fileName = data.data.assetInfo.name;
          if (libfilefolderDetails.allData) {
            libfilefolderDetails.allData[data.data._id] = data.data;
          } else {
            libfilefolderDetails.allData = {};
            libfilefolderDetails.allData[data.data._id] = data.data;
          }
          this.displayUploadProgress = false;
          console.log('upload file', libfilefolderDetails);
          this.addFile.emit();
        })
      } else {
        this.services.toastr.error("Please select a folder to proceed.");
        return false;
      }
    } else {
      this.services.toastr.error("Please select a folder to proceed.");
      return false;
    }
  }

  cancelSavingFile(){
    this.fileUploadDetails.desc = '';
    this.fileUploadDetails.name = '';
    this.fileUploadDetails.size = '';
    this.fileUploadDetails.type = '';
    this.closeModal.emit();
  }

}



