import { Component, OnInit, AfterViewInit, OnChanges, SimpleChanges, Input, Output, EventEmitter } from '@angular/core';

import { QuestionTypesService } from '../question-types.service';
import { SharedServices } from '../../services/services.module';
import { DropdownService } from './drop-down.service';

import * as $ from 'jquery';
import 'select2';

@Component({
  selector: 'app-drop-down',
  templateUrl: './drop-down.component.html',
  styleUrls: ['../question-templates.scss','./drop-down.component.scss']
})
export class DropDownComponent implements OnInit, OnChanges, AfterViewInit {
  @Input() questionData: any;
  @Input() changesInChild : any;
  @Input() responseData?: any;
  @Input() langDirection: string;
  @Input() optTheme: any;
  @Output() onChange: EventEmitter<any> = new EventEmitter<any>();
  completeQuestion: any;
  quesToDisplay: any;

  constructor(
    private qTypesServ: QuestionTypesService,
    private services: SharedServices,
    private dropdownService: DropdownService
  ) {

  }

  ngOnInit() {
    this.completeQuestion = this.questionData;
    this.setQuestionToBeDisplayed();
  }

  ngAfterViewInit(){
    $(`#q-${this.completeQuestion.data.get('_id').value} .cust-dropdown`).select2({
      placeholder: "Select your option"
    });
    this.responseData && this.setResponse();
    let color = (this.optTheme && this.optTheme.color) || 'rgba(0, 0, 0, 0.3)';
    let selColor = (this.optTheme && this.optTheme.selectedOpt && this.optTheme.selectedOpt.color);
    let selBgColor = (this.optTheme && this.optTheme.selectedOpt && this.optTheme.selectedOpt.backgroundColor);
    this.changeTheme(color, selColor, selBgColor);
    $(`#q-${this.completeQuestion.data.get('_id').value} .cust-dropdown`).on('select2:open', () => {
      $('.select2-search--dropdown .select2-search__field').attr('placeholder', 'Type to search...')
      $('.select2-container.select2-container--default.select2-container--open').addClass(`s-${this.completeQuestion.data.get('_id').value}`);
    });
    let th = this;
    $(`#q-${this.completeQuestion.data.get('_id').value} .cust-dropdown`).on('select2:select', function(e){
      e.params.data && th.onSelectionChange(e.params.data.id);
    });
  }

  ngOnChanges(changes: SimpleChanges) {
    if(this.completeQuestion){
      this.setQuestionToBeDisplayed();
      if(changes.optTheme && changes.optTheme.currentValue && changes.optTheme.currentValue.color){
        this.changeTheme(changes.optTheme.currentValue.color, changes.optTheme.currentValue.selectedOpt.color, changes.optTheme.currentValue.selectedOpt.backgroundColor);
      }
    }
  }

  changeTheme(color: string, selColor: string, selBgColor: string){
    setTimeout(() => {
      let qId: string = this.completeQuestion.data.get('_id').value;
      $(`#s-${qId}`).remove();
      $('body').append(this.dropdownService.getStyleTag(qId, color, selColor, selBgColor));
    },0);
  }

  setQuestionToBeDisplayed(){
    let rowArr = this.completeQuestion.data.get('fieldOptions.row');
    let tempArr = [];
    tempArr = rowArr.controls.filter(opt => (opt.get('isHidden').value == 0 && opt.get('isNa').value == 0));
    if(this.completeQuestion.data.get('properties') && this.completeQuestion.data.get('properties.order').value == 'asc'){
      tempArr = this.services.utils.sortByKey(tempArr, 'asc', 'text');
    }
    this.quesToDisplay = this.qTypesServ.changeForNg2Select(tempArr);
    if(rowArr.at(rowArr.length - 1).get('isNa').value){
      this.quesToDisplay.push(this.qTypesServ.getNg2Item(rowArr.at(rowArr.length - 1), rowArr.length - 1));
    }
  }

  setResponse() {
    this.responseData.forEach(item=>{
      let row = this.completeQuestion['data'].get('fieldOptions.row').value.find(row=>row._id==item.rowId);
      if(row.text) {
          $(`#q-${this.completeQuestion.data.get('_id').value} .cust-dropdown`).val(item.rowId);
          $(`#q-${this.completeQuestion.data.get('_id').value} .cust-dropdown`).trigger("change");
      }
      // $(`#q-${this.completeQuestion.data.get('_id').value} .cust-dropdown`).trigger('change');
    });
  }

  onSelectionChange(id) {
    let qId = this.completeQuestion['data'].get('_id').value;
    let ansData = event ? [{rowId: id}] : [];
    this.onChange.emit({
      qId: qId,
      ansData: ansData,
      validPresent: false,
      validated:  false,
      addtionalTextPresent: false,
      additionaTextValidationPresent: false,
      additionaTextValidated: false,
      completedResp: (ansData.length == 1)
    });
  }
}



