import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'app-slider-type2',
  templateUrl: './slider-type2.component.html',
  styleUrls: ['./slider-type2.component.css', '../slider.component.scss']
})
export class SliderType2Component implements OnInit {
  @Input() sliderData: any;
  @Input() rating: any;
  @Output() onChangeSubSlider = new EventEmitter<any>();

  constructor() { }

  ngOnInit() {}

  onChange(){
    this.onChangeSubSlider.emit({"rating":this.rating});
  }


}



