import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { SurveyValidationService } from '../../../services/validation.module';
import { SharedServices } from '../../../services/services.module';

@Component({
  selector: 'app-graphical-emojis',
  templateUrl: './graphical.emojis.component.html',
  styleUrls: ['./graphical.emojis.component.scss', '../graphical-rating.component.scss', '../../question-templates.scss']
})
export class GraphicalEmojisComponent implements OnInit {
  @Input() completeQuestion;
  @Input() rowId;
  @Input() addTxtInp;
  @Input() answer: number;
  @Output() onChange: EventEmitter<any> = new EventEmitter<any>();
  emojiIcon: any = {
    'faces': {
      '2': ['very-sad', 'very-happy'],
      '3': ['very-sad', 'nuetral', 'very-happy'],
      '4': ['very-sad', 'sad', 'happy', 'very-happy'],
      '5': ['very-sad', 'sad', 'nuetral', 'happy', 'very-happy']
    },
    'cartoon': {
      '2': ['cartoon-very-sad', 'cartoon-very-happy'],
      '3': ['cartoon-very-sad', 'cartoon-neutral', 'cartoon-very-happy'],
      '4': ['cartoon-very-sad', 'cartoon-sad', 'cartoon-happy', 'cartoon-very-happy'],
      '5': ['cartoon-very-sad', 'cartoon-sad', 'cartoon-neutral', 'cartoon-happy', 'cartoon-very-happy']
    },
    'funky': {
      '2': ['funky-very-sad', 'funky-very-happy'],
      '3': ['funky-very-sad', 'funky-neutral', 'funky-very-happy'],
      '4': ['funky-very-sad', 'funky-sad', 'funky-happy', 'funky-very-happy'],
      '5': ['funky-very-sad', 'funky-sad', 'funky-neutral', 'funky-happy', 'funky-very-happy']
    }
  }
  rating: any;
  additinalColText: Array<any> = [];
  addTxt: any;
  vldObjCol: Array<any> = [];
  colMain: any;
  constructor(
    private valdServ: SurveyValidationService,
    private services: SharedServices
  ) { }

  ngOnInit() {
    console.log("addTxtInp",this.addTxtInp)
    for(let i=0; i < this.completeQuestion.data.get('fieldOptions.col').value.length; i++){
      if(this.completeQuestion.data.get('fieldOptions.col').at(i).get('hasText').value){
        this.additinalColText[this.completeQuestion.data.get('fieldOptions.col').at(i).get('_id').value] = "";
      }
    }
    if(this.addTxtInp){
      this.colMain = Object.keys(this.addTxtInp)[0];
      this.addTxt = this.addTxtInp[Object.keys(this.addTxtInp)[0]];
      this.additinalColText[this.colMain] = this.addTxt;
    }
    console.log("completeQuestion",this.completeQuestion.data.get('fieldOptions.col').value)
    this.rating = this.answer;
    if(!this.rating[this.rowId]) {
      this.rating[this.rowId] = 1;
    } else {
      this.rating[this.rowId] = this.answer[this.rowId].toString();
    }
  }

  changeOpt(colId, addChange=false){
    (!this.colMain || !addChange)?this.colMain = colId:'';
    this.addTxt = this.additinalColText[this.colMain];
    console.log("this.additinalColText",this.additinalColText)
    console.log("rt",this.rating[this.rowId])
    this.onChange.emit({
      colIdx: this.rating[this.rowId],
      additionalText: this.addTxt,
      rowId: this.rowId,
      colMain: this.colMain,
      additionalTextPresent: this.addTxtInp,
      additionaTextValidationPresent: this.addTxt && this.vldObjCol[colId].msg,
      additionaTextValidated: this.addTxt && this.vldObjCol[colId].ret,
      completedResp: (this.rating[this.rowId] >= 1)
    });
  }

  setAddColText(colId){
    if(this.additinalColText[colId])
      this.vldObjCol[colId] = this.valdServ.checkAdditionalTextValidation('', this.services.form.formObj.getQuestionById(this.completeQuestion.data.get('_id').value).getOptionById(colId), this.additinalColText[colId].trim());

    this.changeOpt(colId, true);
  }

}



