import { Injectable } from '@angular/core';

@Injectable()
export class Ng2CropperService {
  constructor() {}

  encode(blob) {
    return new Promise((res, rej) => {
      let reader = new FileReader();
      reader.onload = function(e) {
        res(e.target['result']);
      };
      reader.readAsDataURL(blob);
    });
  };

  decode(dataUrl) {
    let meta = dataUrl.split(';')[0];
    let type = meta.split(':')[1];
    let binary = atob(dataUrl.split(',')[1]);
    let array = new Uint8Array(binary.length);
    for (let i = 0; i < binary.length; i++) {
        array[i] = binary.charCodeAt(i);
    }
    return new Blob([array], {type: type});
  };

  crop(file, data) {
    let _decodeBlob = this.decode;
    return this.encode(file).then(this._createImage).then((image) => {
      let canvas = this.createCanvas(data);
      let context = canvas.getContext('2d');
      let element: any = image;
      context.drawImage(element, data.x, data.y, data.width, data.height, 0, 0, data.width, data.height);

      let encoded = canvas.toDataURL(file.type);
      this.removeElement(canvas);

      return _decodeBlob(encoded);
    });
  };

  scale(file, data) {
    let _decodeBlob = this.decode;
    return this.encode(file).then(this._createImage).then((image) => {
      let heightOrig = image['height'];
      let widthOrig = image['width'];
      let ratio, height, width;

      if (Object.prototype.toString.call(data) == "[object Number]") {
        ratio = data;
        height = heightOrig * ratio;
        width = widthOrig * ratio;
      }

      if (Object.prototype.toString.call(data) == "[object Object]") {
        ratio = widthOrig / heightOrig;
        height = data.height;
        width = data.width;

        if (height && !width)
          width = height * ratio;
        else if (width && !height)
          height = width / ratio;
      }

      let canvas = this.createCanvas(data);
      let context = canvas.getContext('2d');

      canvas.height = height;
      canvas.width = width;
      let element: any = image;
      context.drawImage(element, 0, 0, widthOrig, heightOrig, 0, 0, width, height);

      let encoded = canvas.toDataURL(file.type);
      this.removeElement(canvas);

      return _decodeBlob(encoded);
    });
  };


  private _createImage(source) {
    return new Promise((res, rej) => {
      let image = new Image();
      image.onload = function(e) { res(e.target); };
      image.src = source;
    })
  }

  private createCanvas(data) {
    let canvas = document.createElement('canvas');
    canvas.width = data.width;
    canvas.height = data.height;
    canvas.style.display = 'none';
    document.body.appendChild(canvas);
    return canvas;
  }

  private removeElement(el) {
    el.parentElement.removeChild(el);
  }

}


