import { Component, OnInit, OnChanges, SimpleChanges, Input, Output, EventEmitter } from '@angular/core';
import { FormGroup} from '@angular/forms';
import { SharedServices } from '../../services/services.module';
import { SurveyValidationService } from '../../services/validation.module';
@Component({
  selector: 'app-multiple-textboxes',
  templateUrl: './multiple-textboxes.component.html',
  styleUrls: ['../question-templates.scss', './multiple-textboxes.component.scss']
})
export class MultipleTextboxesComponent implements OnInit, OnChanges {
  @Input() questionData: any;
  @Input() changesInChild : any;
  @Input() responseData?: any;
  @Input() langDirection: string;
  @Input() optTheme: any;
  @Output() onChange: EventEmitter<any> = new EventEmitter<any>();
  completeQuestion: any;
  quesToDisplay: any;
  ansData: Array<any> = [];
  textBox: any = {
    text: {},
    addText: {}
  };
  vldObjInp: Array<any> = [];
  vldObjAdd: Array<any> = [];
  sumValidationExists: boolean = false;
  sumValidationHappened: boolean = true;
  sumValidation: number = 0;

  constructor(
    private services: SharedServices,
    private valdServ: SurveyValidationService
  ){ }

  ngOnInit() {
    this.completeQuestion = this.questionData;
    this.setQuestionToBeDisplayed();
    this.responseData && this.setResponse();
  }

  ngOnChanges(changes: SimpleChanges) {
    if(this.completeQuestion){
      this.setQuestionToBeDisplayed();
    }
  }

  setQuestionToBeDisplayed(){
    this.quesToDisplay = this.completeQuestion.data.get('fieldOptions.row').controls.filter(opt => opt.get('isHidden').value == 0);
    this.sumValidationExists = (this.completeQuestion.data.get('validation.valdType') && this.completeQuestion.data.get('validation.valdType').value == "percentageSum")? true: false;
    if(this.sumValidationExists){
        this.sumValidation = this.completeQuestion.data.get('validation.max').value
    }
  }

  setResponse() {
    this.ansData = this.responseData;
    this.responseData.forEach(item=>{
      this.textBox.text[item.rowId] = item.text;
      item.rowText && (this.textBox.addText[item.rowId] = item.rowText);
    });
  }

  onSelectionChange(rowId: string) {
    let qId = this.completeQuestion.data.get('_id').value;
    this.ansData = this.ansData.filter(item=>item.rowId!=rowId);
    this.sumValidationHappened = true;

    if(this.textBox.addText[rowId])
      this.vldObjAdd[rowId] = this.valdServ.checkAdditionalTextValidation(this.services.form.formObj.getQuestionById(this.completeQuestion.data.get('_id').value).getOptionById(rowId), '', this.textBox.addText[rowId].trim());

    if(this.textBox.text[rowId])
      this.vldObjInp[rowId] = this.valdServ.checkValidation(this.services.form.formObj.getQuestionById(this.completeQuestion.data.get('_id').value).getOptionById(rowId), '', this.textBox.text[rowId].trim());

    if(this.textBox.text[rowId] && this.vldObjInp[rowId] && this.vldObjInp[rowId].ret){
      let data = {rowId: rowId, text: this.textBox.text[rowId]};

      if(this.textBox.addText[rowId] && this.vldObjAdd[rowId] && this.vldObjAdd[rowId].ret){
          this.textBox.addText[rowId] && (data['rowText'] = this.textBox.addText[rowId]);
      }
      this.textBox.text[rowId] && this.ansData.push(data);
    }

    let completeness = true;
    for(let i =0; i<this.quesToDisplay.length; i++){
      if(!this.existsInAnsData(this.quesToDisplay[i].get('_id').value)){
        completeness = false;
        break;
      }
    }

    if(completeness && this.sumValidationExists){
      let sum = 0.0;
      for(let i=0; i< this.ansData.length; i++){
        if(isNaN(this.ansData[i].text)){
          this.sumValidationHappened = false;
          break;
        }else{
          sum += parseFloat(this.ansData[i].text);
        }
      }

      if(sum == this.sumValidation){
        this.sumValidationHappened = true;
      }else{
        this.sumValidationHappened = false;
      }
    }

    let addTxtPst = false;
    let addTxtValdPst = false;
    let addTxtValdtd = true;
    for(let roi =0; roi < this.quesToDisplay.length; roi++){
      addTxtPst = this.services.form.formObj.getQuestionById(qId).getOptionById(this.quesToDisplay[roi].get('_id').value).hasText
      if(addTxtPst)
        break;
    }

    for(let roi =0; roi < this.quesToDisplay.length; roi++){
      if(this.textBox.addText[this.quesToDisplay[roi].get('_id').value]
        && this.vldObjAdd[this.quesToDisplay[roi].get('_id').value]
        && this.vldObjAdd[this.quesToDisplay[roi].get('_id').value].msg){
        addTxtValdPst = true;
        break;
      }
    }

    for(let roi =0; roi < this.quesToDisplay.length; roi++){
      if(this.textBox.addText[this.quesToDisplay[roi].get('_id').value]
        && this.vldObjAdd[this.quesToDisplay[roi].get('_id').value]
        && !this.vldObjAdd[this.quesToDisplay[roi].get('_id').value].ret){
        addTxtValdtd = false;
        break;
      }
    }

    this.onChange.emit({
      qId: qId,
      ansData: this.ansData,
      validPresent: this.textBox.text[rowId] && this.vldObjInp[rowId] && this.vldObjInp[rowId].msg,
      validated: this.textBox.text[rowId] && this.vldObjInp[rowId] && this.vldObjInp[rowId].ret,
      addtionalTextPresent: addTxtPst,
      additionaTextValidationPresent: addTxtValdPst,
      additionaTextValidated: addTxtValdtd,
      completedResp: (completeness && this.sumValidationHappened)
    });
  }

  existsInAnsData(rId){
    let ret = false;
    for(let i=0; i< this.ansData.length; i++){
      if(this.ansData[i].text && this.ansData[i].rowId == rId){
        ret = true;
        break;
      }
    }
    return ret;
  }

}



