import { Injectable, Inject } from '@angular/core';
import { DOCUMENT} from '@angular/common';
import { PageScrollConfig, PageScrollService, PageScrollInstance } from 'ngx-page-scroll';

@Injectable()
export class UtilsService {

  constructor(
    private pageScrollService: PageScrollService,
    @Inject(DOCUMENT) private document: any
  ) {
    PageScrollConfig.defaultEasingLogic = {
      ease: (t: number, b: number, c: number, d: number): number => {
        // easeInOutExpo easing
        if (t === 0) return b;
        if (t === d) return b + c;
        if ((t /= d / 2) < 1) return c / 2 * Math.pow(2, 10 * (t - 1)) + b;
        return c / 2 * (-Math.pow(2, -10 * --t) + 2) + b;
      }
    };
    PageScrollConfig.defaultDuration = 500;
  }

  cloneObject(obj: any) {
    if(typeof obj === "object") {
      return JSON.parse(JSON.stringify(obj));
    } else {
      console.log('invalid type of clone object', (typeof obj));
      return obj;
    }
  }

  groupBy(array: Array<any>, f:Function) {
    var groups = {};
    array.forEach((o) => {
      var group = JSON.stringify(f(o));
      groups[group] = groups[group] || [];
      groups[group].push(o);
    });
    return Object.keys(groups).map(group => groups[group]);
  }

  findIndex(list: Array<any>, key: any, value: any) {
    let index: number = -1;
    if(list.findIndex) {
      index = list.findIndex(x=>x[key]==value);
    } else {
      let indexes = list.map((obj, index) => {
        if(obj[key] == value) {
          return index;
        }
      }).filter(isFinite);
      index = indexes.length ? indexes[0] : -1;
    }
    return index;
  }

  getTextOutOfHtml(str: string){
    let obj: any = document.createElement('p');
    obj.innerHTML = str;
    return obj.textContent;
  }

  insertContentInActiveTinyMce(str: string){
    tinymce.activeEditor.execCommand('mceInsertContent', false, str);
  }

  setFocusInActiveTinyMce(el: any){
    tinymce.setActive(tinymce.editors[el.id])
    tinymce.activeEditor.focus();
    tinymce.activeEditor.selection.select(tinymce.activeEditor.getBody(), true);
    tinymce.activeEditor.selection.collapse(false);
  }

  scrollPage(id: string, scrollOffset?: number) {
    scrollOffset && (PageScrollConfig.defaultScrollOffset = scrollOffset);
    let pageScrollInstance: PageScrollInstance = PageScrollInstance.simpleInstance(this.document, `#${id}`);
    this.pageScrollService.start(pageScrollInstance);
  }

  objectToArray(obj: any){
    let arr: Array<any> = [];
    for(let item in obj){
      arr.push(obj[item]);
    }
    return arr;
  }

  extendObject(extendFrom: object, extendTo: object): object {
    for(let key in extendFrom) {
      !extendTo[key] && (extendTo[key] = extendFrom[key]);
    }
    return extendTo;
  }

  extendObjectValue(extendFrom: object, extendTo: object): object {
    let obj = this.cloneObject(extendTo);
    for(let key in obj) {
      if(key != 'piping'){
        typeof(extendFrom[key])!="undefined" && (obj[key] = extendFrom[key]);
      }else{
        obj['piping'] = {
          hasPiping: extendFrom['piping'].hasPiping,
          pipedQuestions: []
        }
      }
    }
    return obj;
  }

  sortByKey(arr: Array<any>, str: string, key: string): Array<any> { //str-->['asc','desc']
    let numArr = arr.filter((item) => !isNaN(Number(this.getTextOutOfHtml(item.get(key).value))));
    let strArr = arr.filter((item) => isNaN(Number(this.getTextOutOfHtml(item.get(key).value))));
    let strRes = strArr.sort((a, b) => {
      let x = this.getTextOutOfHtml(a.get(key).value);
      let y = this.getTextOutOfHtml(b.get(key).value);
      if(str == 'asc') return ((x < y) ? -1 : ((x > y) ? 1 : 0));
      else return ((x > y) ? -1 : ((x < y) ? 1 : 0));
    });
    let numRes = numArr.sort((a, b) => {
      let x = this.getTextOutOfHtml(a.get(key).value);
      let y = this.getTextOutOfHtml(b.get(key).value);
      if(str == 'asc') return (x - y);
      else return (y - x);
    });
    return numRes.concat(strRes);
  }

  isFolderNameValid(name: string){
    try {
      let regex = /^[\d\(\)\-\_\w\s]+$/ig;
      return regex.test(name);
    } catch (e) {
      return true;
    }
  }

  getUnique4lengthStr(){
    return Math.random().toString(36).substr(2, 4);
  }

  trackByIndex(index, item) {
    return index;
  }

  trackById(index, item) {
    return item._id;
  }

  selectElementContents(el: any) {
    let range = document.createRange();
    range.selectNodeContents(el);
    let sel = window.getSelection();
    sel.removeAllRanges();
    sel.addRange(range);
  }

  mergeObjs(objectArr) {
    let res: any = {};
    objectArr.forEach((obj) => {
      for (let i in obj)
        res[i] = obj[i];
    });
    return res;
  }

  getCookieByName(name: string): any[] {
    return document.cookie.split('; ').map(function(item){return item.split('=')}).find(function(item){return item[0]==name}) || [];
  }
  reverseString(str) {
    try {
        if(str == '')
            return str;
        let newString = "";
        for (let i = str.length - 1; i >= 0; i--) {
            newString += str[i];
        }
        return newString;
        /* OR code can be
        return str.split("").reverse().join("");
        */
    } catch(err) {
        console.log(err);
    }
  }
}



