import { Component, OnInit, OnChanges, SimpleChanges, Input, Output, EventEmitter } from '@angular/core';

import { QuestionTypesService } from '../question-types.service';
import { SurveyValidationService } from '../../services/validation.module';
import { SharedServices } from '../../services/services.module';

@Component({
  selector: 'app-contacts',
  templateUrl: './contacts.component.html',
  styleUrls: ['../question-templates.scss', './contacts.component.scss']
})
export class ContactsComponent implements OnInit, OnChanges {
  @Input() questionData: any;
  @Input() changesInChild : any;
  @Input() responseData?: any;
  @Input() langDirection: string;
  @Input() optTheme: any;
  @Output() onChange: EventEmitter<any> = new EventEmitter<any>();
  completeQuestion: any;
  quesToDisplay: any;
  ansData: Array<any> = [];
  answers: any = {};
  vldObj: Array<any> = [];

  constructor(
    private qTypesServ: QuestionTypesService,
    private valdServ: SurveyValidationService,
    private services: SharedServices
  ) { }

  ngOnInit() {
    this.completeQuestion = this.questionData;
    this.setQuestionToBeDisplayed();
    this.responseData && this.setResponse();
  }

  ngOnChanges(changes: SimpleChanges) {
    if(this.completeQuestion){
      this.setQuestionToBeDisplayed();
    }
  }

  setQuestionToBeDisplayed(){
    this.quesToDisplay = this.completeQuestion.data.get('fieldOptions.row').controls.filter(opt => opt.get('isHidden').value == 0);
  }

  setResponse() {
    this.responseData.forEach(item=>{
      this.answers[item.rowId] = item.text;
      this.ansData.push({rowId: item.rowId, text:  item.text});
    });
  }

  onSelectionChange(rowId: string) {
    let qId = this.completeQuestion.data.get('_id').value;
    this.vldObj[rowId] = {};
    this.vldObj[rowId].ret = true;
    this.vldObj[rowId].msg = "";

    this.ansData = this.ansData.filter(item=>item.rowId!= rowId);
    if(this.answers[rowId]) {
      this.vldObj[rowId] = this.valdServ.checkValidation(this.services.form.formObj.getQuestionById(qId).getOptionById(rowId), '', this.answers[rowId].trim());

      if(this.vldObj[rowId].ret)
        this.ansData.push({rowId: rowId, text: this.answers[rowId]});
    }

    this.onChange.emit({
      qId: qId,
      ansData: this.ansData,
      validPresent: (this.answers[rowId] && this.vldObj[rowId].msg),
      validated:  (this.answers[rowId] && this.vldObj[rowId].ret),
      addtionalTextPresent: false,
      additionaTextValidationPresent: false,
      additionaTextValidated: false,
      completedResp: (this.quesToDisplay.length == this.ansData.length)
    });
  }

}



