import { Component, OnInit, OnChanges, SimpleChanges, Input, Output, EventEmitter } from '@angular/core';
import { QuestionTypesService } from '../question-types.service';
import { SurveyValidationService } from '../../services/validation.module';
import { MomentModule } from 'angular2-moment/moment.module';
import { SharedServices } from '../../services/services.module';
import * as moment from 'moment';

@Component({
  selector: 'app-date-and-time',
  templateUrl: './date-and-time.component.html',
  styleUrls: ['../question-templates.scss', './date-and-time.component.scss']
})
export class DateAndTimeComponent implements OnInit, OnChanges {
  @Input() questionData: any;
  @Input() changesInChild : any;
  @Input() responseData: any;
  @Input() langDirection: string;
  @Input() optTheme: any;
  @Output() onChange: EventEmitter<any> = new EventEmitter<any>();
  ansData: Array<any>;
  ans: string;
  addText: string = "";
  completeQuestion: any;
  quesToDisplay: any;
  dateFormat: any;
  mask: any;
  vldObj: Array<any> = [];

  constructor(
    private quesTypesService: QuestionTypesService,
    private valdServ: SurveyValidationService,
    private services: SharedServices
  ) { }

  ngOnInit() {
    this.completeQuestion = this.questionData;
    this.setQuestionToBeDisplayed();
    this.dateFormat = this.quesTypesService.questionTypesRelatedData.dateTimeFormatJSON;
    this.responseData && this.setResponse();
  }

  ngOnChanges(changes: SimpleChanges) {
    if(this.completeQuestion){
      this.setQuestionToBeDisplayed();
    }
  }

  setQuestionToBeDisplayed(){
    this.quesToDisplay = this.completeQuestion.data.get('fieldOptions.row').controls.filter(opt => opt.get('isHidden').value == 0);
  }

  setResponse() {
    this.ansData = this.responseData;
    if(this.responseData[0]) {
      this.ans = moment(this.responseData[0].text).format(this.getDateTimeMask().format);
      this.responseData[0].rowText && (this.addText = this.responseData[0].rowText);
    }
  }

  updateResponse(rowId: string) {
    this.ansData = [];
    let qId = this.completeQuestion.data.get('_id').value;

    this.vldObj[rowId] = {};
    this.vldObj[rowId].ret = true;
    this.vldObj[rowId].msg = "";

    if(this.addText) {
      this.vldObj[rowId] = this.valdServ.checkAdditionalTextValidation(this.services.form.formObj.getQuestionById(qId).getOptionById(rowId), '', this.addText.trim());
    }

    if(!this.ans){
      this.onChange.emit({
        qId: qId,
        ansData: this.ansData,
        validPresent: false,
        validated: false,
        addtionalTextPresent: this.services.form.formObj.getQuestionById(qId).getOptionById(rowId).hasText,
        additionaTextValidationPresent: this.addText && this.vldObj[rowId].msg,
        additionaTextValidated: this.addText && this.vldObj[rowId].ret,
        completedResp: (this.ansData.length == 1)
      });
      return;
    }

    let validation: any = this.getDateValidation();
    let date = moment(this.ans, this.getDateTimeMask().format);

    if (date && !isNaN(date.toDate().getTime())) {
      let item: any = {text: date.toDate().getTime(), rowId: rowId};
      if(this.vldObj[rowId].ret){
         this.addText && (item.rowText = this.addText);
      }
      this.ansData.push(item);
    }

    this.onChange.emit({
      qId: qId,
      ansData: this.ansData,
      validPresent: false,
      validated: false,
      addtionalTextPresent: this.services.form.formObj.getQuestionById(qId).getOptionById(rowId).hasText,
      additionaTextValidationPresent: this.vldObj[rowId].msg && this.addText,
      additionaTextValidated: this.vldObj[rowId].ret && this.addText,
      completedResp: (this.ansData.length == 1)
    });
  }

  getIntlDate(date: Date): Date {
    let oldDate = date.getDate();
    date.setDate(date.getMonth() + 1);
    date.setMonth(oldDate - 1);
    return date;
  }

  getDateTimeMask(){
    if(this.completeQuestion.data.get('fieldType.subtype').value == 'dateOnly'){
      if(this.completeQuestion.data.get('validation.text').value == 'date_intl'){ //dd/mm/yyyy
        return {
          mask: [/[0-3]/, /[0-9]/, '/', /[0-1]/, /[0-9]/, '/', /[1-9]/, /\d/, /\d/, /\d/],
          placeholder: 'dd/mm/yyyy',
          format: 'DD/MM/YYYY'
        }
      }else if(this.completeQuestion.data.get('validation.text').value == 'date_us'){ //mm/dd/yyyy
        return {
          mask: [/[0-1]/, /[0-9]/, '/', /[0-3]/, /[0-9]/, '/', /[1-9]/, /\d/, /\d/, /\d/],
          placeholder: 'mm/dd/yyyy',
          format: 'MM/DD/YYYY'
        }
      }
    }else if(this.completeQuestion.data.get('fieldType.subtype').value == 'timeOnly'){  //hh:mm
      return {
        mask: [/[0-2]/, /\d/, ':', /\d/, /\d/],
        placeholder: 'hh:mm',
        format: 'HH:mm'
      }
    }else if(this.completeQuestion.data.get('fieldType.subtype').value == 'both'){
      if(this.completeQuestion.data.get('validation.text').value == 'date_intl'){ //dd/mm/yyyy hh:mm
        return {
          mask: [/[0-3]/, /[0-9]/, '/', /[0-1]/, /[0-9]/, '/', /[1-9]/, /\d/, /\d/, /\d/, ' ', /[0-2]/, /\d/, ':', /\d/, /\d/],
          placeholder: 'dd/mm/yyyy hh:mm',
          format: 'DD/MM/YYYY HH:mm'
        }
      }else if(this.completeQuestion.data.get('validation.text').value == 'date_us'){ //mm/dd/yyyy hh:mm
        return {
          mask: [/[0-1]/, /[0-9]/, '/', /[0-3]/, /[0-9]/, '/', /[1-9]/, /\d/, /\d/, /\d/, ' ', /[0-2]/, /\d/, ':', /\d/, /\d/],
          placeholder: 'mm/dd/yyyy hh:mm',
          format: 'MM/DD/YYYY HH:mm'
        }
      }
    }
  }

  getDateValidation(): any {
    let subType = this.completeQuestion.data.get('fieldType.subtype').value;
    let text = this.completeQuestion.data.get('validation.text').value;
    return {type: subType, text: text};
  }

}



