import { Component, OnInit, OnChanges, SimpleChanges, Input, Output, EventEmitter } from '@angular/core';
import { QuestionTypesService } from '../../question-types.service';

@Component({
  selector: 'app-nps-arc',
  templateUrl: './nps-arc.component.html',
  styleUrls: ['./nps-arc.component.scss']
})
export class NpsArcComponent implements OnInit, OnChanges {
  @Input() npsColorArr: any;
  @Input() npsFontColorArr: any;
  @Input() allowColor: any;
  @Input() respones: any;
  @Input() completeQuestion: any;
  @Input() optTheme: any;
  @Output() onChange: EventEmitter<any> = new EventEmitter<any>();
  answer: string = "";
  textColor: any = {
    unsel: 'white'
  }
  selColorToApply: Boolean = false;

  constructor(
    private qTypesServ: QuestionTypesService
  ) { }

  ngOnInit() {
    this.respones && this.respones.forEach(item=>{
      let colIdx = this.qTypesServ.getIndexById(this.completeQuestion.data.get('fieldOptions.col').value, item.colId);
      this.answer = colIdx+"";
    });
    this.selColorToApply = !this.allowColor && this.optTheme && !this.optTheme.selectedOpt.color;
  }

  ngOnChanges(changes: SimpleChanges){

  }

  changeCircularNps(e: string){
    this.answer = e;
    this.onChange.emit(this.answer);
  }

}



