import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'app-slider-type1',
  templateUrl: './slider-type1.component.html',
  styleUrls: ['./slider-type1.component.scss', '../slider.component.scss']
})
export class SliderType1Component implements OnInit {
  @Input() sliderData: any;
  @Input() rating: any;
  @Output() onChangeSubSlider = new EventEmitter<any>();

  constructor() { }

  ngOnInit() {
  }

  onChange(){
    this.onChangeSubSlider.emit({"rating":this.rating});
  }

}



