import { Directive, Output,Input,ElementRef, HostListener, EventEmitter } from '@angular/core';

@Directive({ selector: '[appS2cZoom]' })
export class S2cZoomDirective {
  @Input() imageHeight: any;
  @Input() imageWidth: any;

  constructor(private el: ElementRef) { console.log("el",el)}

  @HostListener('mousewheel', ['$event']) onMouseWheelChrome(event: any) {
    this.mouseWheelFunc(event);
  }

  @HostListener('DOMMouseScroll', ['$event']) onMouseWheelFirefox(event: any) {
    this.mouseWheelFunc(event);
  }

  @HostListener('onmousewheel', ['$event']) onMouseWheelIE(event: any) {
    this.mouseWheelFunc(event);
  }

  mouseWheelFunc(event: any) {
    console.log("eve",event.wheelDelta)
    event = window.event || event; // old IE support
    let delta = Math.max(-1, Math.min(1, (event.wheelDelta || event.detail)));
    console.log("delta",delta)
    if(delta > 0) {
        this.mouseWheelUp(event);
    } else if(delta < 0) {
        this.mouseWheelDown(event);
    }
    // for IE
    event.returnValue = false;
    // for Chrome and Firefox
    if(event.preventDefault) {
        event.preventDefault();
    }
  }

  mouseWheelUp(event) {
    console.log('mouse wheel up');
    this.imageWidth = this.imageWidth+10;
    this.imageHeight = this.imageHeight+10;
    this.el.nativeElement.style.width = this.imageWidth+"px";
    this.el.nativeElement.style.height = this.imageHeight+"px";
    this.el.nativeElement.style['background-size'] = this.imageHeight+"px";
  }

  mouseWheelDown(event) {
    console.log('mouse wheel down');
    this.imageWidth = this.imageWidth-10;
    this.imageHeight = this.imageHeight-10;
    this.el.nativeElement.style.width = this.imageWidth+"px";
    this.el.nativeElement.style.height = this.imageHeight+"px";
    this.el.nativeElement.style['background-size'] = this.imageHeight+"px";
  }

}



