import { Component, OnInit, OnChanges, SimpleChanges, Input, Output, EventEmitter, ViewChild } from '@angular/core';
import { FormGroup } from '@angular/forms';

import { QuestionTypesService } from '../question-types.service';
import { SurveyValidationService } from '../../services/validation.module';
import { SharedServices } from '../../services/services.module';

@Component({
  selector: 'app-single-choice',
  templateUrl: './single-choice.component.html',
  styleUrls: ['../question-templates.scss', './single-choice.component.scss']
})
export class SingleChoiceComponent implements OnInit, OnChanges {
  @Input() questionData : any;
  @Input() changesInChild : any;
  @Input() responseData?: any;
  @Input() langDirection: string;
  @Input() optTheme: any;
  @Output() onChange: EventEmitter<any> = new EventEmitter<any>();
  completeQuestion: any;
  quesToDisplay: any;
  ansData: Array<any> = [];
  addText: any = {};
  answer: string;
  vldObj: Array<any> = [];

  constructor(
    private qTypesServ:QuestionTypesService,
    private valdServ: SurveyValidationService,
    private services: SharedServices
  ) { }

  ngOnInit() {
    this.completeQuestion = this.questionData;
    this.setQuestionToBeDisplayed();
    this.responseData && this.setResponse();
  }

  ngOnChanges(changes: SimpleChanges) {
    if(this.completeQuestion){
      this.setQuestionToBeDisplayed();
    }
  }

  setQuestionToBeDisplayed(){
    this.quesToDisplay = this.completeQuestion.data.get('fieldOptions.row').controls.filter(opt => opt.get('isHidden').value == 0);
  }

  setResponse() {
    this.ansData = this.responseData;
    this.responseData.forEach(item=>{
      if(!item.text) {
        this.answer = item.rowId;
        item.rowText && (this.addText[item.rowId] = item.rowText);
      } else {
        this.addText[item.rowId] = item.text;
      }
    });
  }

  setAdditionalText(rowId: string, isComment: boolean) {
    if(this.addText[rowId])
      this.vldObj[rowId] = this.valdServ.checkAdditionalTextValidation(this.services.form.formObj.getQuestionById(this.completeQuestion.data.get('_id').value).getOptionById(rowId), '', this.addText[rowId].trim());

    if(this.addText[rowId] && this.vldObj[rowId] && this.vldObj[rowId].ret){
      if(isComment) {
        this.ansData = this.ansData.filter(item=>item.rowId!= rowId);
        if(this.addText[rowId]) {
          this.ansData.push({rowId: rowId, text: this.addText[rowId]});
        }
      } else {
        this.ansData.filter(item=>item.rowId==rowId).forEach(item=>item.rowText=this.addText[rowId]);
      }
    }

    this.updateResponse();
  }

  onSelectionChange(rowId: string) {
    // console.log("this.answer",this.answer)
    if(this.answer && rowId == this.answer){
      this.answer = null;
      this.ansData = [];
      this.updateResponse();
    }else{
      this.answer = rowId;
      let data = {rowId: rowId};
      this.addText[rowId] && this.vldObj[rowId] && this.vldObj[rowId].ret && (data['rowText'] = this.addText[rowId]);
      this.ansData = [data];
      this.updateResponse();
    }
  }

  updateResponse() {
    let qId = this.completeQuestion.data.get('_id').value;
    console.log("this.ansData",this.ansData)
    // console.log("this.answer",this.answer)
    let addTxtPst = false;
    let addTxtOptSelecd = false;
    for(let roi =0; roi < this.quesToDisplay.length; roi++){
      addTxtPst = this.services.form.formObj.getQuestionById(qId).getOptionById(this.quesToDisplay[roi].get('_id').value).hasText
      if(addTxtPst)
        break;
    }

    if(this.answer && this.services.form.formObj.getQuestionById(qId).getOptionById(this.answer).hasText){
        addTxtOptSelecd = true;
    }else{
        addTxtOptSelecd = false;
    }

    let addTxtValdPst = false;
    let addTxtValdtd = true;
    for(let roi =0; roi < this.quesToDisplay.length; roi++){
      if(this.addText[this.quesToDisplay[roi].get('_id').value]
        && this.vldObj[this.quesToDisplay[roi].get('_id').value].msg){
        addTxtValdPst = true;
        break;
      }
    }

    for(let roi =0; roi < this.quesToDisplay.length; roi++){
      if(this.addText[this.quesToDisplay[roi].get('_id').value]
        && !this.vldObj[this.quesToDisplay[roi].get('_id').value].ret){
        addTxtValdtd = false;
        break;
      }
    }

    this.onChange.emit({
      qId: qId,
      ansData: this.ansData,
      validPresent: false,
      validated: false,
      overallAddnTxtPresent: addTxtPst,
      addtionalTextPresent: addTxtOptSelecd,
      additionaTextValidationPresent: addTxtValdPst,
      additionaTextValidated: addTxtValdtd,
      completedResp: (this.ansData.length >= 1)
    });
  }

}



