import { Component, OnInit, OnChanges, Input, ChangeDetectorRef, SimpleChanges, Output, EventEmitter } from '@angular/core';
import { FormArray, FormGroup} from '@angular/forms';
import { BarRatingModule } from 'ngx-bar-rating';
import { QuestionTypesService } from '../question-types.service';
import { UtilsService } from '../../services/utils.service';
import { SurveyValidationService } from '../../services/validation.module';
import { SharedServices } from '../../services/services.module';

@Component({
  selector: 'app-graphical-rating',
  templateUrl: './graphical-rating.component.html',
  styleUrls: ['../question-templates.scss', './graphical-rating.component.scss']
})
export class GraphicalRatingComponent implements OnInit, OnChanges {
  @Input() questionData: any;
  @Input() changesInChild: any;
  @Input() responseData?: any;
  @Input() rateVisible: boolean;
  @Input() langDirection: string;
  @Input() optTheme: any;
  @Output() onChange: EventEmitter<any> = new EventEmitter<any>();
  completeQuestion: any;
  quesToDisplay: any = {
    rows: [],
    cols: []
  }
  colArr: any;
  rateFamily: any;
  rate: any = {};
  graphicalJSON: any;
  ansData: Array<any> = [];
  textBoxs: any = {
    col: {},
    row: {},
    naOption: {},
    comment: {}
  };
  vldObjRow: Array<any> = [];
  vldObjCol: Array<any> = [];

  constructor(
    private changeDetectorRef: ChangeDetectorRef,
    private questionTypesService: QuestionTypesService,
    private utils: UtilsService,
    private valdServ: SurveyValidationService,
    private services: SharedServices
  ) { }

  ngOnInit() {
    this.completeQuestion = this.questionData;
    this.setQuestionToBeDisplayed();
    this.colArr = this.completeQuestion.data.get('fieldOptions.col');
    this.responseData && this.setResponse();
    this.rateFamily = ((<FormArray>this.completeQuestion.data.get('fieldOptions.row')).controls[0].get('optType.family'));
    this.graphicalJSON = this.questionTypesService.questionTypesRelatedData.graphicalRatingTypeJSON;
  }

  ngOnChanges(changes: SimpleChanges){
    if(this.completeQuestion){
      this.setQuestionToBeDisplayed();
    }
    for(let propName in changes){
      let chng = changes[propName];
      this.completeQuestion && this.completeQuestion.data && this.changeDetectorRef.detectChanges();
    }
  }

  setQuestionToBeDisplayed(){
    this.quesToDisplay.rows = this.completeQuestion.data.get('fieldOptions.row').controls.filter(opt => opt.get('isHidden').value == 0);
    this.quesToDisplay.cols = this.completeQuestion.data.get('fieldOptions.col').controls.filter(opt => opt.get('isHidden').value == 0);
  }

  setResponse() {
    this.ansData = this.responseData;
    let columns = this.completeQuestion.data.get('fieldOptions.col').value;
    let colLength = columns.length;
    let isNa = columns[colLength-1].isNa;
    isNa && (colLength--);
    this.responseData.forEach(item=>{
      if(!item.text) {
        let index = this.utils.findIndex(columns, '_id', item.colId);
        if(index < colLength) {
          // set rating
          this.rate[item.rowId] = index + 1;
        } else {
          // set N/A option
          this.textBoxs.naOption[item.rowId] = true;
        }
        item.rowText && (this.textBoxs.row[item.rowId] = item.rowText);
        item.colText && (this.textBoxs.col[item.colId] = item.colText);
      } else {
        this.textBoxs.comment[item.rowId] = item.text;
      }
    });
  }

  setAddRowText(rowId: string) {
    // console.log("rowaddPrev",this.ansData)
    if(this.textBoxs.row[rowId])
      this.vldObjRow[rowId] = this.valdServ.checkAdditionalTextValidation(this.services.form.formObj.getQuestionById(this.completeQuestion.data.get('_id').value).getOptionById(rowId), '', this.textBoxs.row[rowId].trim());
    // console.log("row",this.completeQuestion.data.get('_id').value)
    //
    if(this.textBoxs.row[rowId] && this.vldObjRow[rowId].ret){
      this.ansData.filter(item=>item.rowId==rowId && !item.text).forEach(item=>item.rowText=this.textBoxs.row[rowId]);
      // console.log("rowadd",this.ansData)
    }
    this.updateResponse();
  }

  setAddColText(colId: string) {
    // console.log("coladdPrev",this.ansData)
    if(this.textBoxs.col[colId])
      this.vldObjCol[colId] = this.valdServ.checkAdditionalTextValidation('', this.services.form.formObj.getQuestionById(this.completeQuestion.data.get('_id').value).getOptionById(colId), this.textBoxs.col[colId].trim());

    if(this.textBoxs.col[colId] && this.vldObjCol[colId].ret){
      this.ansData.filter(item=>item.colId==colId && !item.text).forEach(item=>item.colText=this.textBoxs.col[colId]);
      // console.log("coladd",this.ansData)
    }
    this.updateResponse();
  }

  setNAOption(rowId: string) {
    this.rate[rowId] = 0;
    if(this.textBoxs.naOption[rowId]) {
      this.setItemId(rowId);
    } else {
      this.ansData = this.ansData.filter(item => item.rowId!=rowId);
      this.updateResponse();
    }
  }

  setRating(rowId: string) {
    delete this.textBoxs.naOption[rowId];
    this.setItemId(rowId);
  }

  setItemId(rowId: string) {
    this.ansData = this.ansData.filter(item => item.rowId!=rowId);
    // console.log("ansdata",this.ansData)
    // console.log("textBoxs",this.textBoxs)
    // console.log("rate",this.rate)
    try {
      let data = {rowId: rowId};
      if(this.textBoxs.comment[rowId]) {
        data['text'] = this.textBoxs.comment[rowId];
      } else {
        let colIndex = this.rate[rowId] ? this.rate[rowId] : this.completeQuestion.data.value.fieldOptions.col.length;
        data['colId'] = this.completeQuestion.data.value.fieldOptions.col[colIndex - 1]._id;
        this.textBoxs.col[data['colId']] && (data['colText'] = this.textBoxs.col[data['colId']]);
      }
      this.textBoxs.row[rowId] && (data['rowText'] = this.textBoxs.row[rowId]);
      this.updateResponse(data);
    } catch (ex) {}
  }

  setComment(rowId: string) {
    this.ansData = this.ansData.filter(item=>item.rowId!=rowId);
    let text = this.textBoxs.comment[rowId];
    let data;

    if(text)
      this.vldObjRow[rowId] = this.valdServ.checkAdditionalTextValidation(this.services.form.formObj.getQuestionById(this.completeQuestion.data.get('_id').value).getOptionById(rowId), '', this.textBoxs.comment[rowId].trim());

    if(text && this.vldObjRow[rowId].ret) {
      data = {rowId: rowId};
      data['text'] = text;
      this.textBoxs.row[rowId] && (data['rowText'] = this.textBoxs.row[rowId]);
    }
    this.updateResponse(data);
  }

  updateResponse(data?: any) {
    data && this.ansData.push(data);
    console.log(this.ansData);

    let qId = this.completeQuestion.data.get('_id').value;
    let completeness = false;
    for(let rowix=0; rowix<this.quesToDisplay.rows.length; rowix++){
      console.log("roww",this.quesToDisplay.rows[rowix])
      if(this.checkCommentExists(rowix)){
        console.log("comm",this.quesToDisplay.rows[rowix].get('isComment').value)
        completeness = true;
      }else if(this.quesToDisplay.rows[rowix].get('isComment').value == 0){
        if(this.ifExistsRowIxInAnsData(rowix)){
          completeness = true;
        }else{
          completeness = false;
        }
      }else{
        completeness = false;
      }

      if(!completeness)
        break;
    }

    let allRowsCommentFlag = true;
    for(let rowix=0; rowix<this.quesToDisplay.rows.length; rowix++){
      if(!this.quesToDisplay.rows[rowix].get('isComment').value){
          allRowsCommentFlag = false;
          break;
      }
    }

    if(allRowsCommentFlag)
      completeness = true;

    let addTxtPst = false;
    let addTxtValdPst = false;
    let addTxtValdtd = true;
    for(let roi =0; roi < this.quesToDisplay.rows.length; roi++){
      addTxtPst = this.services.form.formObj.getQuestionById(qId).getOptionById(this.quesToDisplay.rows[roi].get('_id').value).hasText
      if(addTxtPst)
        break;
    }

    if(!addTxtPst){
      for(let coi =0; coi < this.quesToDisplay.cols.length; coi++){
        addTxtPst = this.services.form.formObj.getQuestionById(qId).getOptionById(this.quesToDisplay.cols[coi].get('_id').value).hasText
        if(addTxtPst)
          break;
      }
    }

    for(let roi =0; roi < this.quesToDisplay.rows.length; roi++){
      if(this.textBoxs.row[this.quesToDisplay.rows[roi].get('_id').value]
          && !this.quesToDisplay.rows[roi].get('isComment').value
          && this.vldObjRow[this.quesToDisplay.rows[roi].get('_id').value].msg){
        addTxtValdPst = true;
        break;
      }
    }

    if(!addTxtValdPst){
      for(let coi =0; coi < this.quesToDisplay.cols.length; coi++){
        if(this.textBoxs.col[this.quesToDisplay.cols[coi].get('_id').value]
          && this.vldObjCol[this.quesToDisplay.cols[coi].get('_id').value].msg){
          addTxtValdPst = true;
          break;
        }
      }
    }

    for(let roi =0; roi < this.quesToDisplay.rows.length; roi++){
      if(this.textBoxs.row[this.quesToDisplay.rows[roi].get('_id').value]
         && !this.quesToDisplay.rows[roi].get('isComment').value
         && !this.vldObjRow[this.quesToDisplay.rows[roi].get('_id').value].ret){
        addTxtValdtd = false;
        break;
      }
    }

    if(addTxtValdtd){
      for(let coi =0; coi < this.quesToDisplay.cols.length; coi++){
        if(this.textBoxs.col[this.quesToDisplay.cols[coi].get('_id').value]
          && !this.vldObjCol[this.quesToDisplay.cols[coi].get('_id').value].ret){
          addTxtValdtd = false;
          break;
        }
      }
    }

    console.log("this.ansData",this.ansData);
    this.onChange.emit({
      qId: qId,
      ansData: this.ansData,
      validPresent: false,
      validated: false,
      addtionalTextPresent: addTxtPst,
      additionaTextValidationPresent: addTxtValdPst,
      additionaTextValidated: addTxtValdtd,
      completedResp: completeness
    });
  }

  checkCommentExists(rowix){
    if(this.quesToDisplay.rows[rowix].get('isComment').value && this.textBoxs.comment[this.quesToDisplay.rows[rowix].get('_id').value]
      && this.vldObjRow[this.quesToDisplay.rows[rowix].get('_id').value] && this.vldObjRow[this.quesToDisplay.rows[rowix].get('_id').value].ret)
      return true;
    else if(this.quesToDisplay.rows[rowix].get('isComment').value && !this.textBoxs.comment[this.quesToDisplay.rows[rowix].get('_id').value]){
      for(let ansix = 0; ansix < this.ansData.length; ansix++){
        console.log("this.ansData[ansix]",this.ansData[ansix])
        if((this.ansData[ansix].rowId != this.quesToDisplay.rows[rowix].get('_id').value)
          && !this.services.form.formObj.getQuestionById(this.completeQuestion.data.get('_id').value).getOptionById(this.ansData[ansix].rowId).isComment){
          return true;
        }
      }
      return false;
    }else{
      return false;
    }
  }

  ifExistsRowIxInAnsData(rowix){
    for(let ansix = 0; ansix < this.ansData.length; ansix++){
      console.log("this.ansData[ansix]",this.ansData[ansix])
      if(this.ansData[ansix].rowId == this.quesToDisplay.rows[rowix].get('_id').value){
        return true;
      }
    }
    return false;
  }

  onChangeItem(event: any) {
    console.log(event);

    if(event.additionalTextPresent && event.additionalText && event.colMain
      && ((event.additionaTextValidationPresent && event.additionaTextValidated) || event.additionaTextValidationPresent) ){
      this.textBoxs.col[event.colMain] = event.additionalText;
      this.setAddColText(event.colMain)
    }
    this.rate[event.rowId] = event.colIdx;
    delete this.textBoxs.naOption[event.rowId];
    this.setItemId(event.rowId);
  }

}



