import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';

import { QuestionTypesService } from '../question-types.service';
import { UtilsService } from '../../services/utils.service';
import { SurveyValidationService } from '../../services/validation.module';

@Component({
  selector: 'app-comment-box',
  templateUrl: './comment-box.component.html',
  styleUrls: ['../question-templates.scss', './comment-box.component.scss']
})
export class CommentBoxComponent implements OnInit {
  @Input() questionData: any;
  @Input() responseData?: any;
  @Input() langDirection: string;
  @Input() optTheme: any;
  @Output() onChange: EventEmitter<any> = new EventEmitter<any>();
  completeQuestion: any;
  quesJSON: Array<{text: string, family: string, subtype: string, icon: string, row:boolean, col: boolean, forceAlignment: boolean, randomize: boolean}>;
  answer: string;
  vldObj: any = {ret: true, msg: ""};
  ansData: Array<any> = [];

  constructor(
    private questionTypesService:QuestionTypesService,
    private utils: UtilsService,
    private valdServ: SurveyValidationService
  ) { }

  ngOnInit() {
    this.completeQuestion = this.questionData;
    this.quesJSON = this.questionTypesService.questionTypesJSON;
    this.responseData && this.setResponse();
  }

  setResponse() {
    this.responseData.forEach(item=>{
      this.answer = item.text;
    });
    this.onSelectionChange();
  }

  onSelectionChange() {
    let qId = this.completeQuestion['data'].get('_id').value;
    this.ansData = [];
    this.vldObj.ret = true;

    if(this.answer){
      this.vldObj = this.valdServ.checkValidation(this.completeQuestion['data'].get('fieldOptions.row').value[0], '', this.answer.trim());
      if(this.vldObj.ret)
        this.ansData = [{rowId: this.completeQuestion['data'].get('fieldOptions.row').value[0]._id, text: this.answer}];
    }

    this.onChange.emit({
      qId: qId,
      ansData: this.ansData,
      validPresent:  this.answer && this.vldObj.msg,
      validated: this.answer  && this.vldObj.ret,
      addtionalTextPresent: false,
      additionaTextValidationPresent: false,
      additionaTextValidated: false,
      completedResp: (this.ansData.length == 1)
    });

  }

  getText(str: string){
    return this.utils.getTextOutOfHtml(str);
  }

}



