import { Pipe, PipeTransform } from '@angular/core';
import { FormService } from '../services/form.service';
import { DomSanitizer } from '@angular/platform-browser';
import { TakeSurveyService } from '../../routes/take-survey/services/take.survey.service';
import { SurveyResponseService } from '../../routes/take-survey/services/survey.response.service';
import { SharedServices } from '../services/services.module';


@Pipe({  //get array of keys of an object
  name: 'keys',
  pure: false
})
export class KeysPipe implements PipeTransform {

  transform(obj: any): any[] {
    let keys = [];
    for (let key in obj) {
        if(obj.hasOwnProperty(key)) {
            keys.push(key);
        }
    }
    return keys;
  }
}

@Pipe({
  name: 'quesInPiping',  // get the required set of questions for piping
  pure: false
})
export class QuesInPipingPipe implements PipeTransform {

  transform(arr: any[]): any[] {
    let qArr = [];
    for (let item of arr) {
      if(item.type.type == 'single-choice' || item.type.type == 'mul-choice' || item.type.type == 'dropdown' ||
            item.type.type == 'nps' || item.type.type == 'single-text' || item.type.type == 'comment'){
        qArr.push(item);
      }
    }
    return qArr;
  }
}

@Pipe({
  name: 'swapMongoIdAndQuesNum'  // flag ----> true => mongo id to ques num , false => ques num to mongo id
})
export class SwapMongoIdAndQuesNum implements PipeTransform {
  constructor(private form: FormService){}

  transform(str: any, flag: boolean, _id: string): string {
    if(this.form.getPipingObject()) {
      if(flag){  //mongo id to ques num
        if(this.form.getPipingObject()[_id]){
          this.form.getPipingObject()[_id].forEach((qId) => {
            str = str.replace(new RegExp('{{p://' + qId + '}}', 'g'), '{{p://Q' + this.form.getQuestionCountAndId()['data'][qId] + '}}');
          })
        }
      }else{  //ques num to mongo id
        if(this.form.getPipingObject()[_id]) {
          this.form.getPipingObject()[_id].forEach((qId) => {
            str = str.replace(new RegExp('{{p://Q' + this.form.getQuestionCountAndId()['data'][qId] + '}}', 'g'), '{{p://' + qId + '}}');
          })
        }
      }
    }
    return str;
  }
}

@Pipe({
  name: 'safeHtml'
})
export class SafeHtmlPipe implements PipeTransform {
  constructor(private sanitized: DomSanitizer) {}
  transform(value: string): any {
    return this.sanitized.bypassSecurityTrustHtml(value);
  }
}

@Pipe({
  name: 'reverseString',
})
export class ReverseStringPipe implements PipeTransform {
  constructor() {}
  transform(str: string): any {
      try {
          if(str == '')
              return str;
          let newString = "";
          for (let i = str.length - 1; i >= 0; i--) {
              newString += str[i];
          }
          return newString;
          /* OR code can be
          return str.split("").reverse().join("");
          */
      } catch(err) {
          console.log(err);
      }
  }
}

export const CommonPipes = [KeysPipe, QuesInPipingPipe, SwapMongoIdAndQuesNum, SafeHtmlPipe, ReverseStringPipe];



