import { Component, OnInit, Input, ElementRef } from '@angular/core';

import { Observable, BehaviorSubject } from 'rxjs';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/toPromise';
import * as jq from 'jquery';
import './cropper/cropper.min.js';

@Component({
  selector: '[cropper]',
  template: '',
  styleUrls: ['./cropper/cropper.css']
})
export class Ng2CropperComponent implements OnInit {
  @Input() options: any;
  private imageSrc: string;

  constructor(
    private ref: ElementRef
  ) { }

  ngOnInit() {
    setTimeout(() => {
      this.imageSrc = this.ref.nativeElement.src;
      jq(this.ref.nativeElement).cropper(this.options);
    }, 0);
  }

  ngDoCheck() {
    if(this.ref.nativeElement.src && (this.ref.nativeElement.src != this.imageSrc)) {
      jq(this.ref.nativeElement).cropper('destroy');
      this.ngOnInit();
    }
  }

  preprocess(options: any, img: any): Observable<any> {
    options = options || {};
    let result = Promise.resolve(options); // No changes.
    if (options.maximize) {
      result = this.maximizeSelection(options, img);
    }
    return Observable.fromPromise(result);
  }

  /**
   * Change options to make selection maximum for the image.
   * fengyuanchen/cropper calculates valid selection's height & width
   * with respect to `aspectRatio`.
   */
  maximizeSelection(options: any, img: any): Promise<any> {
    return this.getRealSize(img).then(function(size) {
      options.data = size;
      return options;
    });
  }

  /**
   * Returns real image size (without changes by css, attributes).
   */
  getRealSize(img: any): Promise<any> {
    return new Promise((res, rej) => {
      let size = {height: null, width: null};
      let image = new Image();

      image.onload = function() {
        res({width: image.width, height: image.height});
      }

      image.src = img.src;
    });
  }

}



