import { Directive, ElementRef, Renderer2, HostListener } from '@angular/core';

@Directive({
  selector: '[appAdditionalTextInput]'
})
export class AdditionalTextInputDirective {
  elem: ElementRef;

  constructor(private elemRef: ElementRef, private renderer: Renderer2) {
    this.elem = this.elemRef;
  }

  @HostListener('focus', ['$event']) onFocus(e) {
      this.renderer.addClass(this.elem.nativeElement, 'inner_addtn_text');
      setTimeout(() => {
        this.renderer.addClass(this.elem.nativeElement, 'focus_addtn_text');
      }, 1)
      return;
    }
    @HostListener('blur', ['$event']) onblur(e) {
      this.renderer.removeClass(this.elem.nativeElement, 'inner_addtn_text');
      this.renderer.removeClass(this.elem.nativeElement, 'focus_addtn_text');
      return;
    }



}



