import { NgModule } from '@angular/core';
import { Http, RequestOptions } from '@angular/http';
import { AuthHttp, AuthConfig } from 'angular2-jwt';

import { UtilsService } from './utils.service';

export function authHttpServiceFactory(http: Http, options: RequestOptions, utils: UtilsService) {
  let surveyCookie = utils.getCookieByName('survey');
  return new AuthHttp(new AuthConfig({
  	tokenName: 'token',
    noClientCheck: true,
	  tokenGetter: (() => surveyCookie.length == 2 ? surveyCookie[1]: undefined),
	  //globalHeaders: [{'Content-Type':'application/json; charset=UTF-8'}],
  }), http, options);
}

@NgModule({
  providers: [
    {
      provide: AuthHttp,
      useFactory: authHttpServiceFactory,
      deps: [Http, RequestOptions, UtilsService]
    }
  ]
})
export class AuthModule {}


