  getQuestionType(str1: string, str2: string) {
    // console.log(str1, str2);
    var obj: any = {
      type: ''
    }
    for (let q in this.questionTypesJSON) {
      if (this.questionTypesJSON[q].family == str1) {
        if (str2 == undefined) {
          obj.type = q;
          break;
        } else if (this.questionTypesJSON[q].subtype.indexOf(str2) != -1) {
          obj.type = q;
          break;
        }
      }
    }
    // console.log(obj);
    return obj;
  }

