  rowColGroup(str?: string, id?: string): FormGroup {
    let tempId = this.utils.getUnique4lengthStr();
    return this._fb.group({
      _id: (id ? id : tempId),
      weight: 0,
      text: (str ? str : ''),
      hasText: 0,
      isComment: 0,
      isHidden: 0,
      isNa: 0,
      color: '#2392ED',
      optType: this._fb.group({
        family: '',
        subtype: ''
      }),
      items: this._fb.array([
      ]),
      validation: this.validation(),
      addnTextvalidation: this.validation()
    });
  }

  validation(){
    return this._fb.group({
      type: "", //new type of validations: date, number, text
      subType:"", //new sub-type of validations: number -> decimalNumber/wholeNumber/naturalNumber/Integer, text: normalText/email, date: withTime/withoutTime
      family: "", // new family of type : 'individual'/'rowOnly'/'columnOnly'/'all'
      text: "", //Error message
      max: "",
      min: "",
      valdType: ""
    })
  }

  itemGroup(str?: string, id?: string): FormGroup {
    let tempId = this.utils.getUnique4lengthStr();
    return this._fb.group({
      _id: (id ? id : tempId),
      text: (str ? str : '')
    })
  }

  uploadGroup(): FormGroup {
    return this._fb.group({
      fieldVald: this._fb.group({
        fieldType: 'Image',
        format: this.questionTypesRelatedData['fileUploadJSON']['Image'].join(),
        maxFileSize: this.questionTypesRelatedData['fileUploadSizeJSON'][0]
      })
    })
  }

  interchangeTextAndDescForGraphicalQues(ques: any) {
    let temp: any;
    ques.fieldOptions.row.forEach(r => {
      temp = r.desc;
      r.desc = r.text;
      r.text = temp;
    })
  }

  getIndexById(data: Array<any>, id: string) {
    return this.utils.findIndex(data.filter(item => !item.isHidden), '_id', id);
  }

  changeForNg2Select(arr: any){
    let res = [];
    if(arr == null){
        return res;
    }
    arr.forEach((item, idx) => {
      res.push(this.getNg2Item(item, idx));
    })
    return res;
  }

  getNg2Item(item: any, idx: number){
    return {
      id: item.get('_id').value,
      text: (item.get('text').value != '' ? this.utils.getTextOutOfHtml(item.get('text').value) : `Option ${idx + 1}`)
    }
  }


