import { Injectable } from '@angular/core';
import { FormArray, FormControl, FormGroup, FormBuilder } from '@angular/forms';

@Injectable()
export class RankingService {

  private rankedBag: any = {};
  constructor() {

  }

  addOrRemove(rowId, item){
    console.log("item",item);
    console.log("rowId",rowId);
    this.rankedBag[rowId] = item;
    console.log("this.rankedBag",this.rankedBag);
  }

  getRankedBag(){
    let arr = [];
    if(this.rankedBag){
      for(let i=0; i<Object.keys(this.rankedBag).length;i++){
        if(this.rankedBag[Object.keys(this.rankedBag)[i]]){
          arr.push(this.rankedBag[Object.keys(this.rankedBag)[i]])
        }
      }
    }
    return arr;
  }

  getRankOfRowId(rid){
    return this.rankedBag[rid];
  }

}



