import { Component, OnInit, Input, Output, HostListener, EventEmitter } from '@angular/core';
import { FormGroup, FormArray, FormControl } from '@angular/forms';
import { DragulaService } from 'ng2-dragula';

import { QuestionTypesService } from '../question-types.service';
import { SharedServices } from '../../services/services.module';
import { SurveyValidationService } from '../../services/validation.module';
import { RankingService } from './ranking.service';

declare var window;

@Component({
  selector: 'app-ranking',
  templateUrl: './ranking.component.html',
  styleUrls: ['../question-templates.scss', './ranking.component.scss']
})
export class RankingComponent implements OnInit {
  @Input() questionData: any;
  @Input() responseData: any;
  @Input() langDirection: string;
  @Input() optTheme: any;
  @Output() onChange: EventEmitter<any> = new EventEmitter<any>();
  completeQuestion: any;
  changesApplied: boolean;
  data: any = {
    rowLen: 0,
    activeRowLen: 0,
    optionLength: 0
  }
  selectedAns: any = {};
  additionalText: any = {};
  ansData: Array<any> = [];
  dragObj: Array<any> = [];
  nondDragObj: Array<any> = [];
  addText: any = {};
  vldObj: Array<any> = [];
  wind: string = window;

  constructor(
    private rankServ: RankingService,
    private services: SharedServices,
    private dragulaService: DragulaService,
    private qTypesServ: QuestionTypesService,
    private valdServ: SurveyValidationService
  ) { }

  // @HostListener('window:scroll', ['$event'])
  // checkRankingScoll(event){
  //   console.log("event",event);
  // }

  ngOnInit() {
    this.dragObj = [];
    this.completeQuestion = this.questionData;
    this.data.rowLen = (<FormArray>this.completeQuestion.data.get('fieldOptions.row')).length;
    this.data.activeRowLen = this.getActiveRow().length;
    this.onItemDrag();
    if(this.responseData) {
      this.setResponse();
    } else {
      this.initializeDragObj();
    }
    this.setOptionCount();
    this.applyChangesOnItem();
  }

  ngDoCheck(){
    let currRowLen = (<FormArray>this.completeQuestion.data.get('fieldOptions.row')).length;
    let currActiveRowLen = this.getActiveRow().length;
    if(this.data.rowLen == currRowLen && this.data.activeRowLen != currActiveRowLen) {
      this.initializeDragObj();
    }
    if(this.data.rowLen != currRowLen){
      this.initializeDragObj();
    }

    if(this.data.rowLen < currRowLen) {
      this.data.optionLength = this.data.optionLength + 1;
    } else if (this.data.rowLen > currRowLen) {
      this.data.optionLength = this.data.optionLength - 1;
    }
    this.data.rowLen = currRowLen;
    this.data.activeRowLen = currActiveRowLen;
    // this.applyChangesOnItem();
  }

  setResponse() {
    this.ansData = this.responseData;
    let activeRows = this.getActiveRow();
    activeRows.forEach(item => {
      if(this.services.utils.findIndex(this.responseData, 'rowId', item._id) == -1){
        let rowIdx:number = this.services.utils.findIndex(this.completeQuestion.data.get('fieldOptions.row').value, '_id', item._id);
        this.dragObj.push({rowIdx: rowIdx, isNa: false, res: -1, rowId: item._id});
      }
    });
    let isNaRows =
    this.responseData.forEach((item, index) => {
      let isNa = false;
      let rowIdx:number = this.services.utils.findIndex(this.completeQuestion.data.get('fieldOptions.row').value, '_id', item.rowId);
      let row:FormGroup = (<FormGroup>(<FormArray>this.completeQuestion.data.get('fieldOptions.row')).at(rowIdx));
      let colIdx = this.qTypesServ.getIndexById(this.completeQuestion.data.get('fieldOptions.col').value, item.colId);
      let col:FormGroup = (<FormGroup>(<FormArray>this.completeQuestion.data.get('fieldOptions.col')).at(colIdx));
      if(col && col.get('isNa').value) {
        this.nondDragObj.push({rowIdx: rowIdx, isNa: !isNa, res: this.data.activeRowLen, rowText: item.rowText, rowId: item.rowId});
      } else {
        this.dragObj.push({rowIdx: rowIdx, isNa: isNa, res: colIdx, rowText: item.rowText, rowId: item.rowId});
      }
    });
    this.dragObj.sort((a, b) => {return a.res - b.res});
  }

  getActiveRow(): Array<any> {
    return (<FormArray>this.completeQuestion.data.get('fieldOptions.row')).value.filter(item=>!item.isHidden);
  }

  preventDefault(e) {
    e = e || window.event;
    if (e.preventDefault)
        e.preventDefault();
    e.returnValue = false;
  }

  disableScroll() {
    if (window.addEventListener) // older FF
        window.addEventListener('DOMMouseScroll', this.preventDefault, {passive: false});
    window.onwheel = this.preventDefault; // modern standard
    window.onmousewheel = window.document.onmousewheel = this.preventDefault; // older browsers, IE
    window.ontouchmove  = this.preventDefault; // mobile
    window.document.body.style['touch-action'] = 'none';
  }

  enableScroll() {
      if (window.removeEventListener)
          window.removeEventListener('DOMMouseScroll', this.preventDefault, {passive: false});
      window.onmousewheel = window.document.onmousewheel = null;
      window.onwheel = null;
      window.ontouchmove = null;
      window.document.onkeydown = null;
      window.document.body.style['touch-action'] = 'auto';
  }

  onItemDrag(){
    const bag: any = this.dragulaService.find('ranking-bag');
    if (bag !== undefined ) this.dragulaService.destroy('ranking-bag');
    this.dragulaService.setOptions('ranking-bag', {
      moves: function(el, target, source, sibling){
        return !el.classList.contains('naApplied');
      }
    });
    this.dragulaService.drop.subscribe((value) => {
      try{
        if(value[0] == 'ranking-bag') {
          setTimeout(() => {
            this.reOrder(true);
          }, 0);
        }
      }catch(err){console.error(err)}
    });
  }

  initializeDragObj(){
    this.dragObj = [];
    let nondDragObjLength = this.nondDragObj ? this.nondDragObj.length : 0
    this.data.optionLength += nondDragObjLength;
    this.nondDragObj = [];
    (<FormArray>this.completeQuestion.data.get('fieldOptions.row')).controls.forEach((item, count) => {
      if(!item.get('isHidden').value) {
        this.dragObj.push({
          rowIdx: count,
          isNa: false,
          res: -1,
          rowId: item.get('_id').value
        });
      }
    });
  }

  reOrder(flag: boolean){
    let count = 0;
    this.dragObj.forEach((item, index) => {
      if(!this.dragObj[index].isNa && (flag || this.dragObj[index].res != -1)){
        this.dragObj[index].res = count;
        count++;
      }
    });
    this.dragObj.forEach(item=> {
      this.rankServ.addOrRemove(this.completeQuestion.data.get('fieldOptions.row').at(item.rowIdx).value._id, item.res + 1);
      if(item.res != -1) {
        let rowId = this.completeQuestion.data.get('fieldOptions.row').at(item.rowIdx).value._id;
        let colId = this.completeQuestion.data.get('fieldOptions.col').at(item.res).value._id;
        let ans: any = this.ansData.find(ans=>ans.rowId==rowId);
        if(ans) {ans.colId = colId, ans.text = (item.res + 1).toString()}
        else {ans = {rowId: rowId, colId: colId, text: (item.res + 1).toString()};this.ansData.push(ans);}
      }
    });
    this.updateAnswer();
    setTimeout(() => {
      this.applyChangesOnItem();
    }, 0);
  }

  setOptionCount(): void {
    let count = 0;
    for(let i in this.dragObj) {
      !this.dragObj[i].isNa && count++;
    }
    this.data.optionLength = count;
  }

  onChangeItem(event) {
    // console.log(event)
    this.setNaOpt(event);
    this.setOptionCount();
    let row = (<FormGroup>(<FormArray>this.completeQuestion.data.get('fieldOptions.row')).at(event.rIdx));

    this.addText[row.get('_id').value] = event.addText;
    if(event.addText){
      this.vldObj[row.get('_id').value] = this.valdServ.checkAdditionalTextValidation(row.value, '', this.addText[row.get('_id').value]);
    }
    // console.log("this.addText[row.get('_id').value]",this.addText[row.get('_id').value])
    // console.log("this.vldObj[row.get('_id').value].ret",this.vldObj[row.get('_id').value])
    if(event.result != -1) {
      let col;
      if(!event.isNa) {
        col = (<FormGroup>(<FormArray>this.completeQuestion.data.get('fieldOptions.col')).at(event.result));
      } else {
        col = (<FormGroup>(<FormArray>this.completeQuestion.data.get('fieldOptions.col')).at((<FormArray>this.completeQuestion.data.get('fieldOptions.col')).length - 1));
      }
      let data: any = {rowId: row.get('_id').value, colId: col.get('_id').value, text: event.text}
      event.addText && this.vldObj[row.get('_id').value] && this.vldObj[row.get('_id').value].ret ? (data.rowText = event.addText) : delete data.rowText;
      this.updateAnswer(data);
    } else {
      this.updateAnswer({rowId: row.get('_id').value});
    }
    this.applyChangesOnItem();
  }

  updateAnswer(data?: any) {
    if(data) {
      this.ansData = this.ansData.filter(item=>item.rowId!=data.rowId);
      data.colId && this.ansData.push(data);
    }
    let qId = this.completeQuestion.data.get('_id').value;


    let addTxtPst = false;
    for(let roi =0; roi < this.getActiveRow().length; roi++){
      addTxtPst = this.getActiveRow()[roi].hasText;
      if(addTxtPst)
        break;
    }

    let addTxtValdPst = false;
    let addTxtValdtd = true;
    for(let roi =0; roi < this.getActiveRow().length; roi++){
      // console.log("this.getActiveRow()[roi]._id",this.getActiveRow()[roi])
      // console.log("this.addText[this.getActiveRow()[roi]._id]",this.addText[this.getActiveRow()[roi]._id])
      // console.log("this.vldObj[this.getActiveRow()[roi]._id].ret",this.vldObj[this.getActiveRow()[roi]._id])
      if(this.addText[this.getActiveRow()[roi]._id]
        && this.vldObj[this.getActiveRow()[roi]._id].msg){
        addTxtValdPst = true;
        break;
      }
    }

    for(let roi =0; roi < this.getActiveRow().length; roi++){
      // console.log("this.getActiveRow()[roi]._id",this.getActiveRow()[roi])
      // console.log("this.addText[this.getActiveRow()[roi]._id]",this.addText[this.getActiveRow()[roi]._id])
      // console.log("this.vldObj[this.getActiveRow()[roi]._id].ret",this.vldObj[this.getActiveRow()[roi]._id])
      if(this.addText[this.getActiveRow()[roi]._id]
        && !this.vldObj[this.getActiveRow()[roi]._id].ret){
        addTxtValdtd = false;
        break;
      }
    }

    this.onChange.emit({
      qId: qId,
      ansData: this.ansData,
      validPresent: false,
      validated: false,
      addtionalTextPresent: addTxtPst,
      additionaTextValidationPresent: addTxtValdPst,
      additionaTextValidated: addTxtValdtd,
      completedResp: (this.ansData.length == this.data.activeRowLen)
    });
  }

  setNaOpt(item: any): void {
    if(item.isNa) {
      let index = this.services.utils.findIndex(this.dragObj, 'rowIdx', item.rIdx);
      if(this.dragObj[index]) {
        this.dragObj[index].isNa = item.isNa;
        this.dragObj[index].res = item.result;
        this.dragObj[index].rowText = item.rowText;
        this.nondDragObj = this.nondDragObj.concat(this.dragObj.splice(index, 1));
      }
      this.reOrder(false);
    } else {
      let index = this.services.utils.findIndex(this.nondDragObj, 'rowIdx', item.rIdx);
      if(this.nondDragObj[index]) {
        this.nondDragObj[index].isNa = item.isNa;
        this.nondDragObj[index].res = item.result;
        this.nondDragObj[index].rowText = item.rowText;
        this.dragObj = this.dragObj.concat(this.nondDragObj.splice(index, 1));
      }
    }
  }

  applyChangesOnItem() {
    this.changesApplied = !this.changesApplied;
  }

}



