import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'app-graphical-slider',
  templateUrl: './graphical.slider.component.html',
  styleUrls: ['./graphical.slider.component.scss', '../../slider/slider.component.scss']
})
export class GraphicalSliderComponent implements OnInit {
  @Input() completeQuestion: any;
  @Input() rowId: string;
  @Input() answer: number;
  @Output() onChange: EventEmitter<any> = new EventEmitter<any>();
  graphicalSliderConfig: any = {
    connect: [true,false],
    step: 1,
    range: {
      min: 1,
      max: 5
    }
  };
  rating: any;
  sliderData: any;

  constructor() { }

  ngOnInit() {
    this.rating = this.answer;
    if(!this.rating[this.rowId]) {
      this.rating[this.rowId] = 1;
    }

    this.sliderData = {
      type: 'discrete', //['discrete','continous']
      config: this.graphicalSliderConfig,
      allowLabels: false,
      labels: {
      },
      // rating: this.rating[this.rowId],
      discreteArray: ['1','2','3','4','5'],
      allowPips: false,
      tooltip: false
    }
  }

  changeSlider(){
    this.onChange.emit({
      colIdx: this.rating[this.rowId],
      rowId: this.rowId,
      additionalTextPresent: false,
      additionaTextValidationPresent: false,
      additionaTextValidated: false,
      completedResp: (this.rating[this.rowId] >= 1)
    });
  }

  onChangeSlider(event){
    if(event){
        this.rating[this.rowId] = event.rating;
        this.changeSlider();
    }
  }

}



